<script>
import Parse from 'parse';
import CHeader from '@/components/c-header'
import AppFooter from '@/components/footer'

export default {
  name: 'CTicketInbox',
  props: {},
  components: {
    CHeader,
    AppFooter,
  },

  data() {
    return {
      Tickets: [],
      CurrentTab: 'all',
      SelectedTicketIndex: -1,
      TabIndex: 0
    }
  },

  methods: {
    pageNavi(i){
      if(this.TabIndex + i < 0) return;
      if(this.TabIndex + i >= Math.ceil(this.Tickets.length/3.0)) return;
      this.TabIndex += i
      this.SelectedTicketIndex = 0;
    },

    splitTickets(ticketArr){
      var res = [];
      var arr = [];
      for(var ticket of ticketArr){
        if(arr.length >= 3){
          res.push(arr);
          arr = [];
        }
        arr.push(ticket);
      }
      if(arr.length > 0) res.push(arr);
      return res;
    },

    queryTickets() {
      var arr = [];
      if (this.CurrentTab !== 'all') {
        for (var t of this.Tickets) {
          if ((t.Status == 'Pending' && this.CurrentTab == 'pending') || (t.Status == 'Closed' && this.CurrentTab == 'closed')) {
            arr.push(t);
          }
        }
      }
      else {
        arr = this.Tickets;
      }
      return arr;
    },

    switchTab(tab) {
      this.CurrentTab = tab;
      this.SelectedTicketIndex = -1;
    },

    selectIndex(i) {
      this.SelectedTicketIndex = i;
    },

    async getCustomerTickets() {
      var tmp = JSON.parse(await Parse.Cloud.run("GetCustomerTickets", { "CustomerID": Parse.User.current().get("AccountID") }));
      for await (var ticket of tmp) {
        ticket["CategoryOfConcernData"] = JSON.parse(await Parse.Cloud.run("GetCategoryOfConcernData", { "CategoryOfConcernID": ticket.CategoryID }));
      }
      this.Tickets = tmp;
    },
  },

  beforeMount() {
    this.getCustomerTickets();
  },
}
</script>

