<script>
import CHeader from '@/components/c-header';
import AppFooter from '@/components/footer';
import Parse from 'parse';


export default {
  name: 'CIndex',
  components: {
    CHeader,
    AppFooter,
  },
  metaInfo: {
    title: 'C-Index - exported project',
    meta: [
      {
        property: 'og:title',
        content: 'C-Index - exported project',
      },
    ],
  },

  beforeMount() {
    //this runs before page loads
    this.getProducts();
    this.getCategories();
  },

  data() {
    return {
      //Product Variables
      Products: [],
      DailyDiscoverProducts: [],

      //Category
      Categories: [],

      CategoryImages: ["@/assets/images/ProcessedFoods.jpg",
        "../../assets/images/AgriBasedProduct.jpg",
        "../../assets/images/ArtsAndCraft.jpg",
        "../../assets/images/HomeAndFashion.jpg",
        "../../assets/images/Others.jpg",
      ]
    }
  },

  methods: {
    getLowestPrice(arr) {
      return Math.min(...arr.map(item => item.OriginalPrice))
    },

    async getProducts() {
      //this.Products = JSON.parse(await Parse.Cloud.run("GetProducts"));
      var tmp = JSON.parse(await Parse.Cloud.run("GetTopProducts", { "Limit": 20 }));
      for await (var prod of tmp) {
        prod["SKUs"] = JSON.parse(await Parse.Cloud.run("GetProductSKUs", { "ProductID": prod.objectId }));
      }
      this.Products = tmp;

      //Randomize daily discover (TODO)
      tmp = JSON.parse(await Parse.Cloud.run("GetProducts", { "Limit": 20 }));
      for await (prod of tmp) {
        prod["SKUs"] = JSON.parse(await Parse.Cloud.run("GetProductSKUs", { "ProductID": prod.objectId }));
      }
      this.DailyDiscoverProducts = tmp;
      console.log(this.DailyDiscoverProducts);
    },

    async getCategories() {
      this.Categories = JSON.parse(await Parse.Cloud.run("GetCategories"));
    },

    showProduct(productid) {
      this.$router.push({
        name: 'c-product',
        query: {
          ProductID: productid,
        }
      })
    },

    seeAll(type) {
      this.$router.push({
        name: 'sp',
        query: {
          Type: type,
        }
      })
    },

    showCategories(categoryid) {
      this.$router.push({
        name: 'c-homepage-category',
        query: {
          CategoryID: categoryid,
        }
      })
    },
  },
}
</script>

