<script>
import Parse from 'parse';
import SHeader from '@/components/s-header'

export default {
  name: 'SAddNewProductResponsive',
  components: {
    SHeader,
  },

  beforeMount() {
    this.getCategories();
  },

  data() {
    return {
      //Frontend Variables
      raw4prr: ' ',

      //Account Variables
      User: Parse.User.current(),

      //Other Variables
      Categories: [],

      //Product Variables
      SupplierID: '',
      ProductName: '',
      CategoryID: '',
      ImagePreview: ["", "", "", ""],
      ImageIndexArr: {},
      ImageIndex: 0,
      SKUImageKey: '',
      ProductDescription: '',
      DangerousGoods: false,
      raw0uly: ' ',
      raw16t2: ' ',
      FinishedUploading: true,
      FinishedSKUUploading: true,

      //Create data for product variation
      ProductVariations: [{ "Type": "", "Options": [""] }], //FIX (Remove Variation and Option)
      SKUDetails: [
        [{ "SKU": "", "Price": "", "Outgoing_Stock": "", "Stock_on_hand": "", "Weight": "", "Parcel_Size": "", "ImageLink": "" }],
      ],

      SKUDetails2: {},
    }
  },

  metaInfo: {
    title: 'S-Add-new-product-Responsive - exported project',
    meta: [
      {
        property: 'og:title',
        content: 'S-Add-new-product-Responsive - exported project',
      },
    ],
  },

  methods:{
      includeSKU(key){
        this.SKUDetails2[key].Included = true;
      },

      doNotIncludeSKU(key){
        this.SKUDetails2[key].Included = false;
      },

      async onImageSelected(e){
        var file = e.target.files[0];
        var parseFile = new Parse.File(file.name, file);
        this.FinishedUploading = false;
        await parseFile.save().then(()=> {
          if(this.ImageIndex == -1){
            this.ImagePreview.push(parseFile.url());
          }
          else{
            this.ImagePreview[this.ImageIndex] = parseFile.url();
          }
          this.FinishedUploading = true;
        });
      },

      //EDIT THIS
      async onSKUImageSelected(e){
        var file = e.target.files[0];
        var parseFile = new Parse.File(file.name, file);
        this.FinishedSKUUploading = false;
        await parseFile.save().then(()=> {
          this.SKUDetails2[this.SKUImageKey]["ImageLink"] = parseFile.url();
          this.FinishedSKUUploading = true;
        });
      },

      //product images
      uploadImage(index){
        if(!this.FinishedUploading) return;
        this.ImageIndex = index;
        document.getElementById("uploadProductImage").click()
      },

      deleteImage(index){
        this.ImagePreview.splice(index, 1);
        //remove using image delete function
      },

      //EDIT THIS
      SKUImageUpload(key){
        if(!this.FinishedSKUUploading) return;
        this.SKUImageKey = key
        document.getElementById("uploadSKUImage").click()
      },

      async addProduct(){
        var params = {
            "SupplierID" : this.SupplierID,
            "ProductName" : this.ProductName,
            "CategoryID" : this.CategoryID,
            "ImagePreview" : this.ImagePreview,
            "ProductDescription" : this.ProductDescription,
            "DangerousGoods" : this.DangerousGoods,
            "ShopID" : this.User.get("AccountID"),
        }

        var all_array = [];
        var prod_id;
        await Parse.Cloud.run("AddProduct", params).then(async (res)=>{
          for await (var variation of this.ProductVariations){
            var param = {
              "ProductID" : await res,
              "Type" : await variation.Type,
            };
            prod_id = res;
            await Parse.Cloud.run("AddVariationType", param).then(async (res2)=>{
              var options = [];
              for await (var option of variation.Options){
                var param2 = {
                  "VariationTypeID" : await res2,
                  "Option" : await option,
                }
                await Parse.Cloud.run("AddVariationOption", param2).then(async (res3) =>{
                  options.push(res3);
                });
              }
              all_array.push(options);
            });
          }
          alert("Added Product");
        });

        let i = 0;  
        let SKUIDS = this.combination(all_array);
        for await (var key of Object.keys(this.SKUDetails2)){
          if(!this.SKUDetails2[key]["Included"]) continue;
          this.SKUDetails2[key]["Variations"] = SKUIDS[i];
          i++;
          var param3 = {
            //Weight
            //Parcel_Size
            "Discount" : 0,
            "StockOnHand" : Number(this.SKUDetails2[key]["Stock_on_hand"]),
            "OutgoingStock" : Number(this.SKUDetails2[key]["Outgoing_Stock"]),
            "ImagePreview" : this.SKUDetails2[key]["ImageLink"],
            "IncomingStock" : 0,
            "Status" : "Live",
            "TotalStock" : 0,
            "OriginalPrice" : Number(this.SKUDetails2[key]["Price"]),
            "PromotionID" : "",
            "SKU" : this.SKUDetails2[key]["SKU"],
            "Variations" : this.SKUDetails2[key]["Variations"],
            "ProductID" : prod_id,
            "ShopID" : this.User.get("AccountID"),
          }
          await Parse.Cloud.run("AddSKU", param3);
          //alert("Added SKU!");
        }
        //alert("Added all SKUS!");
        //go back
        this.cancelAddProduct()
      },

      addVariation(){
        //this.computeSKUDetails();
        this.ProductVariations.push({"Type" : "", "Options" : [""]});
        //this.SKUDetails.push([{"SKU" : "", "Price": "", "Outgoing_Stock" : "", "Stock_on_hand" : "", "Weight": "", "Parcel_Size": "", "ImageLink" : ""}]);
      },

      removeVariation(index){
        this.ProductVariations.splice(index, 1);
        //this.SKUDetails.splice(index, 1);
      },

      addOption(index){
        this.ProductVariations[index].Options.push("");
        //this.SKUDetails[index].push({"SKU" : "", "Price": "", "Outgoing_Stock" : "", "Stock_on_hand" : "", "Weight": "", "Parcel_Size": "", "ImageLink" : ""});
      },

      removeOption(index1, index2){
        this.ProductVariations[index1].Options.splice(index2, 1);
        //this.SKUDetails[index1].splice(index2, 1);
      },

      combination(args) {
        var r = [], max = args.length-1;
        function helper(arr, i) {
            for (var j = 0, l = args[i].length; j < l; j++) {
                var a = arr.slice(0); // clone arr
                a.push(args[i][j]);
                if (i == max)
                    r.push(a);
                else
                    helper(a, i+1);
            }
        }
        helper([], 0);
        return r;
      },

      computeSKUDetails(){
        let i, j;
        this.SKUDetails2 = {}
        var options = [];
        var result = [];
        for(i = 0; i < this.ProductVariations.length; ++i){
          options.push(this.ProductVariations[i]["Options"]);
        }
        result = this.combination(options);

        for(i = 0; i < result.length; ++i){
          let skuName = this.ProductName;
          for(j = 0; j < result[i].length; ++j){
            skuName += "_" + result[i][j];
          }
          this.SKUDetails2[skuName] = {"SKU" : skuName, "Price": "", "Outgoing_Stock" : "", "Stock_on_hand" : "", "Weight": "", "Parcel_Size": "", "ImageLink" : "", "Variations" : [], "Included" : true};
        }

        console.log(this.SKUDetails2);
      },

      async getCategories(){
        this.Categories = JSON.parse(await Parse.Cloud.run("GetCategories"));
      },

      cancelAddProduct(){
        this.$router.push({
          name: 'sso',
        });
      },
    },
}
</script>

