<template>
  <div v-if="ShowModal" id="myModal" class="modal">
    <!-- Modal content -->
    <div class="modal-content">
      <button @click="closeRating()" class="close">&times;</button>
      <span>Rating: </span>
      <input v-model="ProductRating" type="number"> <br>
      <span>Review:</span>
      <textarea v-model="ProductReview"></textarea>
      <button @click="submitReview()">Submit Review</button>
    </div>
  </div>

  <div class="c-profile-personal-info-container">
    <c-header rootClassName="c-header-root-class-name8"></c-header>
    <div class="c-profile-personal-info-profile-personal-info">

      <div class="c-profile-personal-info-p-m-n-p-p-menu">
        <div class="c-profile-personal-info-container1">
          <img :src="[(UserData.ProfilePicture == '' || UserData.ProfilePicture == undefined) ? 'https://play.teleporthq.io/static/svg/default-img.svg' : UserData.ProfilePicture]" class="c-profile-personal-info-profileimg" />
          <span class="c-profile-personal-info-profilename">{{ Username }}</span>
        </div>


        <div class="c-profile-personal-info-buttons">
          <button @click="switchTab('MyAccount')" class="c-profile-personal-info-button button">
            <svg viewBox="0 0 731.4285714285713 1024" class="c-profile-personal-info-icon">
              <path
                d="M731.429 799.429c0 83.429-54.857 151.429-121.714 151.429h-488c-66.857 0-121.714-68-121.714-151.429 0-150.286 37.143-324 186.857-324 46.286 45.143 109.143 73.143 178.857 73.143s132.571-28 178.857-73.143c149.714 0 186.857 173.714 186.857 324zM585.143 292.571c0 121.143-98.286 219.429-219.429 219.429s-219.429-98.286-219.429-219.429 98.286-219.429 219.429-219.429 219.429 98.286 219.429 219.429z">
              </path>
            </svg>
            <span :class="[currentTab=='MyAccount'? 'c-profile-personal-info-text1' :
            'c-profile-personal-info-text']">My Account</span>
          </button>

          <button @click="switchTab('MyOrders')" class="c-profile-personal-info-button1 button">
            <svg viewBox="0 0 1024 1024" class="c-profile-personal-info-icon02">
              <path
                d="M726 768q34 0 59 26t25 60-25 59-59 25-60-25-26-59 26-60 60-26zM42 86h140l40 84h632q18 0 30 13t12 31q0 2-6 20l-152 276q-24 44-74 44h-318l-38 70-2 6q0 10 10 10h494v86h-512q-34 0-59-26t-25-60q0-20 10-40l58-106-154-324h-86v-84zM298 768q34 0 60 26t26 60-26 59-60 25-59-25-25-59 25-60 59-26z">
              </path>
            </svg>
            <span :class="[currentTab=='MyOrders'? 'c-profile-personal-info-text1' :
            'c-profile-personal-info-text']">My Orders</span>
          </button>

          <button @click="switchTab('Notification')" class="c-profile-personal-info-button2 button">
            <svg viewBox="0 0 1024 1024" class="c-profile-personal-info-icon04">
              <path
                d="M768 682l86 86v42h-684v-42l86-86v-212q0-100 51-174t141-96v-30q0-26 18-45t46-19 46 19 18 45v30q90 22 141 96t51 174v212zM512 938q-36 0-61-24t-25-60h172q0 34-26 59t-60 25z">
              </path>
            </svg>
            <span :class="[currentTab=='Notification'? 'c-profile-personal-info-text1' :
            'c-profile-personal-info-text']">Notifications</span>
          </button>

          <button @click="switchTab('Password')" class="c-profile-personal-info-button3 button">
            <svg viewBox="0 0 658.2857142857142 1024" class="c-profile-personal-info-icon06">
              <path
                d="M182.857 438.857h292.571v-109.714c0-80.571-65.714-146.286-146.286-146.286s-146.286 65.714-146.286 146.286v109.714zM658.286 493.714v329.143c0 30.286-24.571 54.857-54.857 54.857h-548.571c-30.286 0-54.857-24.571-54.857-54.857v-329.143c0-30.286 24.571-54.857 54.857-54.857h18.286v-109.714c0-140.571 115.429-256 256-256s256 115.429 256 256v109.714h18.286c30.286 0 54.857 24.571 54.857 54.857z">
              </path>
            </svg>
            <span :class="[currentTab=='Password'? 'c-profile-personal-info-text1' :
            'c-profile-personal-info-text']">
              Password &amp; Security
            </span>
          </button>

          <button @click="switchTab('PaymentMethods')" class="c-profile-personal-info-button4 button">
            <svg viewBox="0 0 1024 1024" class="c-profile-personal-info-icon08">
              <path
                d="M504 466q44 12 73 24t61 33 49 53 17 76q0 62-41 101t-109 51v92h-128v-92q-66-14-109-56t-47-108h94q8 90 126 90 62 0 89-23t27-53q0-72-128-104-200-46-200-176 0-58 42-99t106-55v-92h128v94q66 16 101 60t37 102h-94q-4-90-108-90-52 0-83 22t-31 58q0 58 128 92z">
              </path>
            </svg>
            <span :class="[currentTab=='PaymentMethods'? 'c-profile-personal-info-text1' :
            'c-profile-personal-info-text']">Payment Methods</span>
          </button>
        </div>
      </div>

      <div v-if="currentTab=='MyAccount'" class="c-profile-personal-info-my-profile">
        <div class="c-profile-personal-info-container2">
          <div class="c-profile-personal-info-container3">
            <div class="c-profile-personal-info-my-profile1">
              <span class="c-profile-personal-info-text5">My Profile</span>
              <span class="c-profile-personal-info-text6">
                Account details are found here
              </span>
            </div>
            <input id="uploadProfileImage" @change="onProfileImageSelected" type="file" hidden accept=".jpeg,.jpg,.png"/>
            <button class="c-profile-personal-info-editprofile button">
              Edit Profile
            </button>
          </div>
          <div class="c-profile-personal-info-container4">
            <div class="c-profile-personal-info-profile">
              <img alt="image" :src="[(UserData.ProfilePicture == '' || UserData.ProfilePicture == undefined) ? 'https://play.teleporthq.io/static/svg/default-img.svg' : UserData.ProfilePicture]"
                class="c-profile-personal-info-profileimg1" />
              <button @click="editProfilePhoto()" class="c-profile-personal-info-changeprofile button">
                Change Profile
              </button>
            </div>
            <div class="c-profile-personal-info-container5">
              <div class="c-profile-personal-info-profiledata">
                <div class="c-profile-personal-info-container6">
                  <span class="c-profile-personal-info-username">Username</span>
                  <span class="c-profile-personal-info-fname">First Name</span>
                  <span class="c-profile-personal-info-mname">Middle Name</span>
                  <span class="c-profile-personal-info-lname">Last Name</span>
                  <span class="c-profile-personal-info-emailadd">
                    Email Address
                  </span>
                  <span class="c-profile-personal-info-contactnum">
                    Contact Number
                  </span>
                  <span class="c-profile-personal-info-address">Address</span>
                  <span class="c-profile-personal-info-birthdate">Birthdate</span>
                </div>
                <div class="c-profile-personal-info-container7">
                  <span class="c-profile-personal-info-usernameinput">
                    {{Username}}
                  </span>
                  <input v-model="UserData.FirstName" class="c-profile-personal-info-fnameinput">
                  <input v-model="UserData.MiddleName" class="c-profile-personal-info-mnameinput">
                  <input v-model="UserData.LastName" class="c-profile-personal-info-lnameinput">
                  <span class="c-profile-personal-info-emailaddinput">
                    {{Email}}
                  </span>
                  <input v-model="PhoneNumber" class="c-profile-personal-info-contactnuminput">
                  <div class="c-profile-personal-info-container8 button">
                    <span class="c-profile-personal-info-addressinput">
                      {{CurrentAddress.Province}}, {{CurrentAddress.Municipality}}, {{CurrentAddress.Barangay}}
                    </span>
                    <svg @click="switchTab('Address')" viewBox="0 0 1024 1024"
                      class="c-profile-personal-info-icon10 button">
                      <path
                        d="M884 300l-78 78-160-160 78-78q12-12 30-12t30 12l100 100q12 12 12 30t-12 30zM128 736l472-472 160 160-472 472h-160v-160z">
                      </path>
                    </svg>
                  </div>
                  <input v-model="UserData.Birthday" type="date" class="c-profile-personal-info-birthmonthselect">
                </div>
              </div>
              <button @click="saveUserData()" class="c-profile-personal-info-save button">Save</button>
            </div>
          </div>
        </div>
      </div>

      <div v-if="currentTab=='MyOrders'" class="c-profile-my-orders-myordersbody">
        <div class="c-profile-my-orders-container03">
          <div class="c-profile-my-orders-container04">
            <button @click="switchTabb('All')"
              :class="[currentTabb == 'All' ? 'c-profile-my-orders-button05 button' : 'c-profile-my-orders-button06 button']">
              <span class="c-profile-my-orders-text05">
                <span>All</span>
                <span></span>
              </span>
            </button>
            <button @click="switchTabb('Pending')"
              :class="[currentTabb == 'Pending' ? 'c-profile-my-orders-button05 button' : 'c-profile-my-orders-button06 button']">
              <span class="c-profile-my-orders-text08"><span>To Pay</span></span>
            </button>
            <button @click="switchTabb('ToShip')"
              :class="[currentTabb == 'ToShip' ? 'c-profile-my-orders-button05 button' : 'c-profile-my-orders-button06 button']">
              <span class="c-profile-my-orders-text10"><span>To Ship</span></span>
            </button>
            <button @click="switchTabb('Shipping')"
              :class="[currentTabb == 'Shipping' ? 'c-profile-my-orders-button05 button' : 'c-profile-my-orders-button06 button']">
              <span class="c-profile-my-orders-text12">
                <span>
                  To Receive
                  <span v-html="rawbw2e"></span>
                </span>
                <span class="c-profile-my-orders-text14">(1)</span>
              </span>
            </button>
            <button @click="switchTabb('Completed')"
              :class="[currentTabb == 'Completed' ? 'c-profile-my-orders-button05 button' : 'c-profile-my-orders-button06 button']">
              <span class="c-profile-my-orders-text15">
                <span>Completed</span>
              </span>
            </button>
            <button @click="switchTabb('Cancelled')"
              :class="[currentTabb == 'Cancelled' ? 'c-profile-my-orders-button05 button' : 'c-profile-my-orders-button06 button']">
              <span class="c-profile-my-orders-text17">
                <span>Cancelled</span>
              </span>
            </button>
          </div>
          <div class="c-profile-my-orders-search-bar">
            <input type="text" class="c-profile-my-orders-textinput input" />
            <svg viewBox="0 0 1024 1024" class="c-profile-my-orders-searchheader button">
              <path
                d="M406 598q80 0 136-56t56-136-56-136-136-56-136 56-56 136 56 136 136 56zM662 598l212 212-64 64-212-212v-34l-12-12q-76 66-180 66-116 0-197-80t-81-196 81-197 197-81 196 81 80 197q0 42-20 95t-46 85l12 12h34z">
              </path>
            </svg>
          </div>
        </div>

        <div v-for="(arr, key, i) in categoryQuery()" :key=i class="c-profile-my-orders-container05">
          <div class="c-profile-my-orders-purchase1">
            <div class="c-profile-my-orders-container06">
              <span class="c-profile-my-orders-purchasefrom">Purchase from</span>
              <div class="c-profile-my-orders-container07">
                <span class="c-profile-my-orders-o-t-o-p-h-u-b-a-l-b-a-y">
                  {{ShopData[key].ShopName}}
                  <!--ShopData[key].ShopName-->
                </span>
                <svg viewBox="0 0 1024 1024" class="c-profile-my-orders-storeicon">
                  <path
                    d="M512 768v-170h-256v170h256zM896 598h-42v256h-86v-256h-170v256h-428v-256h-42v-86l42-214h684l42 214v86zM854 170v86h-684v-86h684z">
                  </path>
                </svg>
              </div>
              
            </div>

            <div v-for="(order, j) in arr" :key=j class="c-profile-my-orders-container08">
              <div class="c-profile-my-orders-container09">
                <div class="c-profile-my-orders-container10">
                  <img :src="[(order.SKUData.ImagePreview == '' || order.SKUData.ImagePreview == undefined) ? 'https://play.teleporthq.io/static/svg/default-img.svg' : order.SKUData.ImagePreview]"
                    class="c-profile-my-orders-inventoryimg" />
                </div>
                <div class="c-profile-my-orders-container11">
                  <span class="c-profile-my-orders-text19">
                    {{order.SKUData.ProductData.ProductName}} - {{order.SKUData.SKU}}
                  </span>
                  <span class="c-profile-my-orders-text20">Php {{order.SKUData.OriginalPrice}}</span>
                  <span class="c-profile-my-orders-text21">Quantity {{order.Quantity}}</span>
                </div>
              </div>
              <div class="c-profile-my-orders-container12">
                <span class="c-profile-my-orders-text21">
                  <span class="c-profile-my-orders-text22">
                    Order Status: {{order.OrderStatusData.OrderStatus}} --- Payment Status:
                    {{order.PaymentDetailData.PaymentStatus}}
                    <span v-html="rawbuip"></span>
                  </span>
                </span>

                <template v-if="order.PaymentDetailData.ShippingTotal != undefined">
                  <span class="c-profile-my-orders-text21">
                    <span class="c-profile-my-orders-text22">
                      Order Total:
                      <span v-html="rawbuip"></span>
                    </span>
                  </span>
                  <span class="c-profile-my-orders-text23">
                    <span class="c-profile-my-orders-text24">P</span>
                    <span class="c-profile-my-orders-text25">hp {{order.PaymentDetailData.TotalPayment}}</span>
                  </span>
                </template>

                <button @click="cancelOrder(order)" v-if="order.OrderStatusData.OrderStatus == 'Pending' "
                  class="c-profile-my-orders-buynowprofile button">
                  Cancel
                </button>

                <button @click="payOrder(order.objectId)"
                  v-if="order.OrderStatusData.OrderStatus == 'Unpaid' && order.PaymentDetailData.PaymentStatus == 'Unpaid' && PaymentMethods[order.PaymentMethodID].PaymentMethod == 'GCash' && order.PaymentDetailData.ShippingTotal != undefined"
                  class="c-profile-my-orders-buynowprofile button">
                  Pay Now
                </button>

                <button @click="payOrder(order.objectId)"
                  v-if="order.OrderStatusData.OrderStatus == 'Unpaid' && order.PaymentDetailData.PaymentStatus == 'Disapproved' && PaymentMethods[order.PaymentMethodID].PaymentMethod == 'GCash' && order.PaymentDetailData.ShippingTotal != undefined"
                  class="c-profile-my-orders-buynowprofile button">
                  Resubmit Payment
                </button>

                <button @click="orderReceived(order)" v-if="order.OrderStatusData.OrderStatus == 'Shipping' && order.PaymentDetailData.PaymentStatus == 'Paid'"
                  class="c-profile-my-orders-buynowprofile button">
                  Order Received
                </button>

                <button @click="rateProduct(order)"
                  v-if="order.OrderStatusData.OrderStatus == 'Completed' && !order.HasRating"
                  class="c-profile-my-orders-buynowprofile button">
                  Rate Product
                </button>

                <div v-if="order.HasRating">
                  <span class="c-profile-my-orders-text21">Rated</span>
                </div>

              </div>
            </div>
          </div>

        </div>
      </div>

      <div v-if="currentTab=='Notification'" class="c-profile-notifications-notificationbody">
        <div class="c-profile-notifications-container2">
          <button class="c-profile-notifications-button5 button">
            <span class="c-profile-notifications-text05">
              <span>Order Updates</span>
            </span>
          </button>
          <span class="c-profile-notifications-text07">Mark all as read</span>
        </div>

        <div v-for="notif in Notifications" :key="notif" class="c-profile-notifications-order1">
          <div class="c-profile-notifications-purchase1">
            <div class="c-profile-notifications-container3">
              <span class="c-profile-notifications-purchasefrom">
                {{notif.Header}}
              </span>
            </div>
            <div class="c-profile-notifications-container4">
              <div class="c-profile-notifications-container5">
                <div class="c-profile-notifications-container6">
                  <img src="https://play.teleporthq.io/static/svg/default-img.svg"
                    class="c-profile-notifications-inventoryimg" />
                </div>

                <div class="c-profile-notifications-container7">
                  <span class="c-profile-notifications-text09">
                    {{notif.Title}}
                  </span>
                  <span class="c-profile-notifications-text10">
                    {{notif.Subtitle}}
                  </span>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>

      <div v-if="currentTab=='Password'" class="c-profile-pass-sec-passecbody">
        <div class="c-profile-pass-sec-container2">
          <div class="c-profile-pass-sec-container3">
            <span class="c-profile-pass-sec-text5">Set Password</span>
            <span class="c-profile-pass-sec-text6">
              For your account&apos;s security, do not share your password with
              anyone else
            </span>
          </div>

          <div class="c-profile-pass-sec-p-a-s-s-w-o-r-d-s">
            <div class="c-profile-pass-sec-container4">
              <span v-if="Email !== undefined" class="c-profile-pass-sec-currentpass">Email: {{Email}}</span>
              <span v-if="Email === undefined" class="c-profile-pass-sec-currentpass">Account is connected via fb /
                google</span>
            </div>
          </div>

          <div v-if="Email !== undefined" class="c-profile-pass-sec-buttons1">
            <button @click="resetPassword()" class="c-profile-pass-sec-changepass button">
              Reset Password
            </button>
          </div>
        </div>
      </div>

      <div v-if="currentTab=='PaymentMethods'" class="c-profile-payment-payment-method">
        <div class="c-profile-payment-container2">
          <span class="c-profile-payment-text5">Payment Method</span>
          <div class="c-profile-payment-payment">
            <span class="c-profile-payment-text6">Gcash</span>
            <div class="c-profile-payment-container3">
              <span class="c-profile-payment-text7">Gcash Number:</span>
              <input v-model="GCashNumber" type="text" placeholder="09xxxxxxxxx" class="input" />
            </div>
            <button @click="saveNumber()" class="c-profile-payment-continue button">Save</button>
          </div>
        </div>
      </div>

      <div v-if="currentTab=='Address'" class="c-profile-personal-info-address-container03">
        <div class="c-profile-personal-info-address-container04">
          <div class="c-profile-personal-info-address-container05">
            <div class="c-profile-personal-info-address-container06">
              <span class="c-profile-personal-info-address-text05">
                My Addresses
              </span>

              <button @click="addNewAddress()" class="button c-profile-personal-info-address-button05">
                Add a New Address
              </button>

            </div>
          </div>
          <span class="c-profile-personal-info-address-text06">
            Manage your shipping addresses
          </span>

          <div v-for="address in Addresses" :key="address" class="c-profile-personal-info-address-container07">
            <div class="c-profile-personal-info-address-container08">
              <div class="c-profile-personal-info-address-container09">
                <svg viewBox="0 0 585.1428571428571 1024" class="c-profile-personal-info-address-icon10">
                  <path
                    d="M438.857 365.714c0-80.571-65.714-146.286-146.286-146.286s-146.286 65.714-146.286 146.286 65.714 146.286 146.286 146.286 146.286-65.714 146.286-146.286zM585.143 365.714c0 34.857-4 70.857-18.857 102.286l-208 442.286c-12 25.143-38.286 40.571-65.714 40.571s-53.714-15.429-65.143-40.571l-208.571-442.286c-14.857-31.429-18.857-67.429-18.857-102.286 0-161.714 130.857-292.571 292.571-292.571s292.571 130.857 292.571 292.571z">
                  </path>
                </svg>
                <div class="c-profile-personal-info-address-container10">
                  <div class="c-profile-personal-info-address-container11">
                    <span class="c-profile-personal-info-address-text07">
                      Full Name
                    </span>
                    <span class="c-profile-personal-info-address-text08">
                      Phone Number
                    </span>
                    <span class="c-profile-personal-info-address-text09">
                      Address
                    </span>
                  </div>
                  <div class="c-profile-personal-info-address-container12">
                    <span class="c-profile-personal-info-address-text10">
                      {{address.Fullname}}
                    </span>
                    <span class="c-profile-personal-info-address-text11">
                      {{address.PhoneNumber}}
                    </span>
                    <div class="c-profile-personal-info-address-container13">
                      <span class="c-profile-personal-info-address-text12">
                        <span>{{address.Street}}</span>
                        <span></span>
                      </span>
                      <span class="c-profile-personal-info-address-text15">
                        <span>{{address.Barangay}}, {{address.Municipality}}</span>
                      </span>
                      <span class="c-profile-personal-info-address-text17">
                        <span></span>
                        <span>{{address.Province}}, {{address.PostalCode}}</span>
                      </span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="c-profile-personal-info-address-container14">
                <button @click="editAddress(address.objectId)" class="c-profile-personal-info-address-button06 button">
                  <span class="c-profile-personal-info-address-text20">Edit</span>
                </button>
                <button @click="setAsDefaultAddress(address.objectId, address.DefaultStatus)"
                  class="c-profile-personal-info-address-button07 button">
                  <span class="c-profile-personal-info-address-text21">
                    <template v-if="address.DefaultStatus">
                      Default
                    </template>
                    <template v-if="!address.DefaultStatus">
                      Set as Default
                    </template>
                  </span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
    <app-footer rootClassName="footer-root-class-name8"></app-footer>
  </div>
</template>

<script>
import Parse from 'parse';
import CHeader from '@/components/c-header'
import AppFooter from '@/components/footer'

export default {
  name: 'CProfilePersonalInfo',
  components: {
    CHeader,
    AppFooter,
  },
  metaInfo: {
    title: 'C-ProfilePersonalInfo - exported project',
    meta: [
      {
        property: 'og:title',
        content: 'C-ProfilePersonalInfo - exported project',
      },
    ],
  },

  data() {
    return {
      Username: '',
      CustomerData: {},
      UserData: {},
      Email: '',
      PhoneNumber: '',
      CurrentAddress: {},
      currentTab: "MyAccount",
      raw3hbn: ' ',
      Notifications: [],
      GCashNumber: '',
      rawkksh: ' ',
      raw6zpl: ' ',
      raw4bpi: ' ',
      raw8qew: ' ',
      Orders: [],
      ShopData: {},
      PaymentMethods: {},
      currentTabb: 'All',
      ShowModal: false,

      ProductRating: '',
      ProductReview: '',
      ReviewOrderStatusID: '',
      ReviewProductID: '',
      ReviewSKUID: '',
      ReviewShopID: '',
      Addresses: [],
    }
  },

  methods: {
    async onProfileImageSelected(e){
      var file = e.target.files[0];
      var parseFile = new Parse.File(file.name, file);
      await parseFile.save().then(()=> {
        this.UserData.ProfilePicture = parseFile.url();
      });
    },

    editProfilePhoto(){
      document.getElementById("uploadProfileImage").click();
    },
    
    //Account Functions
    addNewAddress() {
      this.$router.push({
        name: 'c-setup-info',
        query: {
          CustomerAddressID: "new",
        }
      });
    },

    editAddress(id) {
      this.$router.push({
        name: 'c-setup-info',
        query: {
          CustomerAddressID: id,
        }
      });
    },

    async setAsDefaultAddress(id, b) {
      if (!b) {
        var param = { "CustomerID": Parse.User.current().get("AccountID"), "CustomerAddressID": id };
        await Parse.Cloud.run("SetAsDefaultCustomerAddress", param);
        this.getAddressData();
      }
    },

    async getAddressData() {
      var params = {
        "CustomerID": Parse.User.current().get("AccountID"),
      }
      this.Addresses = JSON.parse(await Parse.Cloud.run("GetCustomerAddresses", params));
    },

    async saveNumber() {
      var params = {
        "CustomerID": Parse.User.current().get("AccountID"),
        "GCashNumber": this.GCashNumber,
      }
      await Parse.Cloud.run("EditCustomer", params).then(() => {
        alert("Data updated!");
      })
    },

    getUserData() {
      this.Email = Parse.User.current().get("email");
      console.log(this.Email);
    },
    resetPassword() {
      Parse.User.requestPasswordReset(this.Email).then(() => {
        alert("An reset email is sent!");
      });
    },

    async getNotifications() {
      this.Notifications = JSON.parse(await Parse.Cloud.run("GetCustomerNotifications", { "CustomerID": Parse.User.current().get("AccountID") }));
      console.log(this.Notifications, "!!!");
    },

    async getCustomerData() {
      this.Username = Parse.User.current().get("username");
      this.CustomerData = JSON.parse(await Parse.Cloud.run("GetCustomerData", { "CustomerID": Parse.User.current().get("AccountID") }));
    },

    async getCustomerOrders() {
      var params = { "CustomerID": Parse.User.current().get("AccountID") };
      var orders = await JSON.parse(await Parse.Cloud.run("GetCustomerOrders", params));

      var output = orders.reduce((acc, item) => {
        if (!acc[item.ShopID]) {
          acc[item.ShopID] = [];
        }
        acc[item.ShopID].push(item);
        return acc;
      }, {});

      for await (let [key, arr] of Object.entries(output)) {
        this.ShopData[key] = JSON.parse(await Parse.Cloud.run("GetShopData", { "ShopID": key }));
        for await (var order of arr) {
          var param = { "OrderID": order.objectId };
          order["OrderStatusData"] = JSON.parse(await Parse.Cloud.run("GetOrderStatusByOrderID", param));
          order["PaymentDetailData"] = JSON.parse(await Parse.Cloud.run("GetPaymentDetailByOrderID", param));
          order["SKUData"] = JSON.parse(await Parse.Cloud.run("GetSKUData", { "SKUID": order.SKUID }));
          order["HasRating"] = await Parse.Cloud.run("OrderStatusRated", { "OrderStatusID": order.OrderStatusData.objectId });
        }
      }

      this.Orders = output;
      console.log(this.Orders,"?");
    },

    async getPaymentMethods() {
      var p = JSON.parse(await Parse.Cloud.run("GetPaymentMethods"));
      for (var obj of p) {
        this.PaymentMethods[obj.objectId] = obj;
      }
    },

    categoryQuery() {
      var category_filter = [];
      if (this.currentTabb !== 'All') {
        for (let [key, arr_of_order] of Object.entries(this.Orders)) {
          var arr = [];
          for (var order of arr_of_order) {
            if (order.OrderStatusData.OrderStatus === this.currentTabb) {
              arr.push(order);
            }
            //Special case
            else if (this.currentTabb === 'Pending' && order.OrderStatusData.OrderStatus === 'Unpaid') {
              arr.push(order);
            }
          }
          if (arr.length > 0) {
            category_filter.push(arr);
          }
        }
        console.log(category_filter);
      }
      else {
        category_filter = this.Orders;
      }
      return category_filter;
    },


    payOrder(id) {
      this.$router.push({
        name: 'c-check-out-upload-receipt',
        query: {
          OrderID: id,
        }
      });
    },

    //CHECKPOINT
    //Add button to repay if disapproved
    async orderReceived(order) {
      //Set OrderStatus to Completed
      var odr = order.OrderStatusData;
      odr["OrderStatusID"] = odr.objectId;
      odr["OrderStatus"] = "Completed";
      await Parse.Cloud.run("EditOrderStatus", odr);
      await Parse.Cloud.run("CompleteOrderStatus", odr);
      await Parse.Cloud.run("AddProductSold", { "ProductID": odr.ProductID });
      console.log("Order is received!");
    },

    async cancelOrder(order) {
      //Set OrderStatus to Canceled
      var odr = order.OrderStatusData;
      odr["OrderStatusID"] = odr.objectId;
      odr["OrderStatus"] = "Cancelled";
      await Parse.Cloud.run("EditOrderStatus", odr);
      console.log("Order is cancelled!");
    },

    async rateProduct(order) {
      this.ShowModal = true;
      this.ReviewOrderStatusID = order.OrderStatusData.objectId;
      this.ReviewProductID = order.SKUData.ProductID;
      this.ReviewSKUID = order.SKUID;
      this.ReviewShopID = order.ShopID;
    },

    closeRating() {
      this.ShowModal = false;
    },

    async submitReview() {
      //Add rating on order
      var param = {
        "CustomerID": Parse.User.current().get("AccountID"),
        "OrderStatusID": this.ReviewOrderStatusID,
        "ProductID": this.ReviewProductID,
        "SKUID": this.ReviewSKUID,
        "Rating": this.ProductRating,
        "Review": this.ProductReview,
        "ShopID": this.ReviewShopID,
      }
      //Create rating
      await Parse.Cloud.run("AddRating", param);
      this.ShowModal = false;
    },

    switchTab(tab) {
      this.currentTab = tab;
    },

    switchTabb(tab) {
      this.currentTabb = tab;
    },

    async getAccountDetails() {
      var acc = Parse.User.current();
      this.Email = acc.get("email");
      this.Username = acc.get("username");
      this.PhoneNumber = acc.get("phone");
      this.UserData = JSON.parse(await Parse.Cloud.run("GetCustomerData", { "CustomerID": Parse.User.current().get("AccountID") }));
      this.CurrentAddress = JSON.parse(await Parse.Cloud.run("GetDefaultCustomerAddress", { "CustomerID": Parse.User.current().get("AccountID") }));
    },

    async saveUserData() {
      var params = {
        "CustomerID": Parse.User.current().get("AccountID"),
        "FirstName": this.UserData.FirstName,
        "MiddleName": this.UserData.MiddleName,
        "LastName": this.UserData.LastName,
        "Birthday": this.UserData.Birthday,
        "ProfilePicture": this.UserData.ProfilePicture,
        //ProfilePic
      }
      console.log(params);
      await Parse.Cloud.run("EditCustomer", params);
      params = {
        "firstname": this.UserData.FirstName,
        "middlename": this.UserData.MiddleName,
        "lastname": this.UserData.LastName,
        "email": this.Email,
        "phone": this.PhoneNumber,
      }
      await Parse.Cloud.run("EditUser", params);

      for (var [key, value] of Object.entries(params)) {
        Parse.User.current().set(key, value);
      }
      await Parse.User.current().save();
      alert("Details Saved!");
    },

    // async switchTab(tab) {
    //   if (tab == 'MyAccount') {
    //     this.gotoPage('c-profile-personal-info');
    //   }
    //   else if (tab == 'MyOrders') {
    //     this.gotoPage('c-profile-my-orders');
    //   }
    //   else if (tab == 'Notification') {
    //     this.gotoPage('c-profile-notifications');
    //   }
    //   else if (tab == 'Password') {
    //     this.gotoPage('c-profile-pass-sec');
    //   }
    //   else if (tab == 'PaymentMethods') {
    //     this.gotoPage('c-profile-payment');
    //   }
    // },

    // async gotoPage(page) {
    //   this.$router.push({
    //     name: page,
    //   })
    // },

  },

  beforeMount() {
    this.getCustomerOrders();
    this.getCustomerData();
    this.getAccountDetails();
    this.getUserData();
    this.getPaymentMethods();
    this.getAddressData();
    this.getNotifications();
    if(this.$route.query.ProfileTab != null){
      this.switchTab(this.$route.query.ProfileTab);
    }
  },
}
</script>

