<script>
import Parse from 'parse';
import CHeader from '@/components/c-header'
import AppFooter from '@/components/footer'

export default {
  name: 'CCheckOut',
  components: {
    CHeader,
    AppFooter,
  },
  metaInfo: {
    title: 'C-Check-Out - exported project',
    meta: [
      {
        property: 'og:title',
        content: 'C-Check-Out - exported project',
      },
    ],
  },

  data() {
    return {
      CustomerCartIDs: [],
      CartDatas: [],
      PaymentMethodID: '',
      DefaultAddress: {},
      PaymentMethods: [],
    }
  },

  methods: {
    getTotalOrder() {
      var total = 0;
      for (var item of this.CartDatas) {
        total += item.SKUData.OriginalPrice * item.Quantity;
      }
      return total;
    },

    deleteItem(index) {
      //do this
      this.CartDatas.splice(index, 1);
    },

    backToCart() {
      this.$router.push({
        name: 'c-cart',
      });
    },

    changeAddress() {
      this.$router.push({
        name: 'c-profile',
      });
    },

    async getDefaultAddress() {
      this.DefaultAddress = JSON.parse(await Parse.Cloud.run("GetDefaultCustomerAddress", {"CustomerID": Parse.User.current().get("AccountID")}));
    },

    async getPaymentMethods() {
      this.PaymentMethods = JSON.parse(await Parse.Cloud.run("GetPaymentMethods"));
    },
    //TOCHECKIFGOOD
    async getCartDataAsOne(id, quantity) {
      var obj = {};
      var skudata = JSON.parse(await Parse.Cloud.run("GetSKUData", { "SKUID": id }));
      obj["SKUData"] = skudata;
      obj["Quantity"] = Number(quantity);
      obj["objectId"] = "None";
      obj["SKUID"] = id;
      obj["ShopID"] = skudata.ShopID;
      this.CartDatas.push(obj);
    },

    async getCartData(arr) {
      if (typeof arr == "string") arr = [arr];
      for (var id of arr) {
        var param = { "CartID": id };
        this.CartDatas.push(JSON.parse(await Parse.Cloud.run("GetCartData", param)));
      }
      console.log(this.CartDatas);
    },

    async createOrders() {
      if (this.CartDatas.length <= 0) {
        alert("No items, Please go back to cart!");
        return;
      }

      if (this.PaymentMethodID == '') {
        alert("Please Choose Payment Method!");
        return;
      }

      var param = {
        "CustomerID": Parse.User.current().get("AccountID"),
        "CustomerAddressID": this.DefaultAddress.objectId,
        "PaymentMethodID": this.PaymentMethodID,
      }
      for await (var data of this.CartDatas) {
        param["MerchandiseSubtotal"] = (data.SKUData.OriginalPrice * data.Quantity);
        param["CartID"] = data.objectId;
        param["ShopID"] = data.ShopID;
        param["SKUID"] = data.SKUID;
        param["ProductID"] = data.SKUData.ProductData.objectId;
        param["Quantity"] = data.Quantity;
        await Parse.Cloud.run("AddOrderDetail", param);
      }
      this.$router.push({
        name: 'c-check-out-pending',
      });
    },
  },

  beforeMount() {
    this.CustomerCartIDs = this.$route.query.CheckOutCartIDs;
    if (this.CustomerCartIDs != undefined) {
      this.getCartData(this.$route.query.CheckOutCartIDs);
    }
    else {
      this.getCartDataAsOne(this.$route.query.SKUID, this.$route.query.Quantity);
    }
    this.getDefaultAddress();
    this.getPaymentMethods();
    console.log(this.CartDatas);
  },
}
</script>

