<script>
import Parse from 'parse';

export default {
  name: 'CCheckOutUploadReceipt',
  metaInfo: {
    title: 'C-Check-OutUpload-Receipt - exported project',
    meta: [
      {
        property: 'og:title',
        content: 'C-Check-OutUpload-Receipt - exported project',
      },
    ],
  },

  data() {
    return {
      OrderID: '',
      PaymentDetailData: {},
      GCashName: '',
      GCashNumber: '',
      ReferenceNumber: '',
    }
  },

  methods: {
    async getOrderData(id) {
      this.PaymentDetailData = JSON.parse(await Parse.Cloud.run("GetPaymentDetailByOrderID", { "OrderID": id }));
    },

    async submitPayment() {
      var param = {
        "ImageReceipt": "Test",
        "ReferenceNumber": this.ReferenceNumber,
        "PaymentDetailID": this.PaymentDetailData.objectId,
        "OrderID": this.OrderID,
        "Name": this.GCashName,
        "Number": this.GCashNumber,
      }
      //Add Gcash
      await Parse.Cloud.run("AddGcash", param);

      //SetPaymentDetail Status to Verifying
      param = { "PaymentDetailID": this.PaymentDetailData.objectId, "PaymentStatus": "Verifying" };
      await Parse.Cloud.run("EditPaymentDetail", param);

      //Go back to myorders
      this.$router.push({
        name: 'c-profile-my-orders',
      });
    },
  },

  beforeMount() {
    this.OrderID = this.$route.query.OrderID;
    this.getOrderData(this.OrderID);
  },
}
</script>

