<script>
import Parse from 'parse';
import CHeader from '@/components/c-header'
import AppFooter from '@/components/footer'

export default {
  name: 'CProduct',
  components: {
    CHeader,
    AppFooter,
  },

  metaInfo: {
    title: 'C-Product - exported project',
    meta: [
      {
        property: 'og:title',
        content: 'C-Product - exported project',
      },
    ],
  },

  data() {
    return {
      ProductData: {},
      ShopData: {},
      ProductSKUs: [],
      CategoryData: [],
      ProductRatings: [],
      RatingCategories: [0, 0, 0, 0, 0],
      TopShopProducts: [],
      CustomerData: {},
      ImageTabIndex: 0,
      ImageSelected: undefined,

      SelectedVariation: "",
      ShippingMode: "COD",
      Quantity: 1,

      //TODO
      Ratings: [],
      Reviews: [],
      TopPicks: [],

      rawnvje: ' ',
      rawdyb7: ' ',
      rawqyzx: ' ',

    }
  },

  async beforeMount() {
    await this.getProductData(this.$route.query.ProductID);
    this.getRatingCategory();
  },

  methods: {
    showProduct(productid) {
      console.log("OK");
      /*
      FIX BUG NEXT TIME
      this.$router.push({
        // same name route
        name: "c-product",
        query: {
          // your new query params here, if any
          ProductID: productid,

          // the random key that I'm using, any uuid should do the trick, only to make the whole
          // route "different"
          _randomKey: (new Date()).getTime()
        }
      });*/
    },

    getHowManyStars(num) {
      var arr = [0, 0, 0] //full star, half star, no star
      arr[0] = num;
      arr[1] += (Math.round(num) != num);
      arr[2] = 5 - num;
      //console.log(arr,"?");
      return arr;
    },

    selectImageShow(img) {
      this.ImageSelected = img;
    },

    toggleImageTab(i, l) {
      if (this.ImageTabIndex + i < 0) return;
      else if (this.ImageTabIndex + i >= l) return;
      this.ImageTabIndex += i;
      console.log(this.ImageTabIndex, l);
    },

    splitProductImages(arr) {
      var res = [];
      var tmp = [];
      if (arr == undefined) return res;
      for (var img of arr) {
        tmp.push(img);
        if (tmp.length >= 5) {
          res.push(tmp);
          tmp = [];
        }
      }
      if (tmp.length > 0) res.push(tmp);
      return res;
    },

    getProductImage(img) {
      if (this.SelectedVariation != "") {
        if (this.SelectedVariation.ImagePreview != "" && this.SelectedVariation.ImagePreview != undefined) {
          return this.SelectedVariation.ImagePreview;
        }
        return "https://play.teleporthq.io/static/svg/default-img.svg"
      }
      else if (this.ImageSelected != undefined) {
        if (this.ImageSelected == '') return "https://play.teleporthq.io/static/svg/default-img.svg";
        return this.ImageSelected;
      }
      else {
        if (img == "") {
          return "https://play.teleporthq.io/static/svg/default-img.svg"
        }
        return img;
      }
    },

    getAverageRating() {
      if (this.ProductRatings.length == 0) {
        return (0).toFixed(1);
      }

      var num = 0;
      for (var rating of this.ProductRatings) {
        num += rating.Rating;
      }
      num /= this.ProductRatings.length;
      return num.toFixed(1);
    },

    getLowestPrice(arr) {
      return Math.min(...arr.map(item => item.OriginalPrice))
    },

    buyNow() {
      this.$router.push({
        name: 'c-checkout',
        query: {
          SKUID: this.SelectedVariation.objectId,
          Quantity: this.Quantity,
        }
      });
    },

    gotoCategory(id) {
      this.$router.push({
        name: 'c-homepage-category',
        query: {
          CategoryID: id,
        }
      });
    },

    modifyQuantity(n) {
      if (this.Quantity + n <= 0 || this.Quantity - n >= 99) {
        return;
      }
      this.Quantity += n;
    },

    getMinMaxPrice() {
      var prices = [];
      for (var sku of this.ProductSKUs) {
        prices.push(sku.OriginalPrice);
      }
      if (prices.length <= 0) {
        return "0 - 0";
      }
      return String(Math.min(...prices)) + " - " + String(Math.max(...prices));
    },

    gotoShop(id) {
      this.$router.push({
        name: 'c-store',
        query: {
          ShopID: id,
        }
      });
    },
    async addToCart() {
      var params = {
        "CustomerID": Parse.User.current().get("AccountID"),
        "SKUID": this.SelectedVariation.objectId,
        "Quantity": Number(this.Quantity),
        "ShopID": this.SelectedVariation.ShopID,
        "ProductID": this.SelectedVariation.ProductID,
      }
      await Parse.Cloud.run("AddCart", params).then(() => {
        this.$router.push({
          name: 'c-cart',
        });
      });
    },

    getRatingCategory() {
      for (var rating of this.ProductRatings) {
        this.RatingCategories[Math.floor(rating.Rating) - 1] += 1;
      }
      //Convert Rating Categories
      for (let i = 0; i < this.RatingCategories.length; ++i) {
        if (this.RatingCategories[i] > 0) {
          this.RatingCategories[i] /= this.ProductRatings.length;
          this.RatingCategories[i] = Number(this.RatingCategories[i].toFixed(1));
        }
      }
    },

    async getProductData(ProductID) {
      var param = { "ProductID": ProductID };
      this.ProductData = JSON.parse(await Parse.Cloud.run("GetProductData", param));
      this.ProductSKUs = JSON.parse(await Parse.Cloud.run("GetProductSKUs", param));
      var tmp = JSON.parse(await Parse.Cloud.run("GetProductRatings", param));
      for await (var rating of tmp) {
        rating["CustomerData"] = JSON.parse(await Parse.Cloud.run("GetCustomerData", { "CustomerID": rating.CustomerID }));
      }
      this.ProductRatings = tmp;

      param = { "CategoryID": this.ProductData.CategoryID };
      this.CategoryData = JSON.parse(await Parse.Cloud.run("GetCategoryData", param));
      param = { "ShopID": this.ProductData.ShopID };
      this.ShopData = JSON.parse(await Parse.Cloud.run("GetShopData", param));
      param["Limit"] = 2;
      tmp = JSON.parse(await Parse.Cloud.run("GetTopShopProducts", param));
      for (var prod of tmp) {
        prod["SKUs"] = JSON.parse(await Parse.Cloud.run("GetProductSKUs", { "ProductID": prod.objectId }));
      }
      this.TopShopProducts = tmp;
    },

  },


}
</script>

