<script>
import Parse from 'parse';
import AppFooter from '@/components/footer'

export default {
  name: 'SignIn',
  props: {},
  components: {
    AppFooter,
  },

  data(){
    return{
      rawiowi: ' ',
      Username: '',
      Password: '',
    }
  },

  methods:{
    createAccount(){
      this.$router.push({
        name: 's-sign-up',
      });
    },

    async signInWithUsername(){
      Parse.User.logIn(this.Username, this.Password).then(()=>{
        this.gotoHomepage();
      });
    },

    gotoHomepage(){
      this.$router.push({
        name: 'sso',
      });
    },

    gotoSignUp(){
      this.$router.push({
        name: 's-sign-up',
        query: {
          "SignUpType": "Facebook",
        }
      });
    },

    async signInWithGoogle(){
        var params = {"AccountType" : "Seller"};
        window.location.href = await Parse.Cloud.run("GoogleSignIn", params);
    },

    async signInWithFacebook() {
        await this.loadFacebookSDK(document, "script", "facebook-jssdk");
        await this.initFacebook();
        await window.FB.login(function(response) {
            var facebookNewUser = false;
            if (response.authResponse) {
                const authData = {
                    "id": response.authResponse.userID,
                    "access_token": response.authResponse.accessToken,
                    "expiration_date": response.authResponse.expiresIn,
                }
                window.FB.api('/me', function(data) {
                    const user = new Parse.User();
                    user.set("username", data.name);
                    var names = data.name.split(' ');
                    user.set("firstname", names[0]);
                    user.set("lastname", names[names.length - 1]);
                    user.set("AccountType", "Seller");
                    user.linkWith("facebook", { authData }).then(async (_obj)=>{
                        await Parse.User.logInWith('facebook', { authData });
                        window.location.pathname = "/s-sign-up"
                    });
                });
            }
            else {
                alert("User cancelled login or did not fully authorize.");
            }
        });
    },

    async initFacebook() {
        return new Promise((resolve, _reject) => {
            window.fbAsyncInit = function() {
                window.FB.init({
                    appId: "490657576400793", //You will need to change this
                    cookie: true, // This is important, it's not enabled by default
                    version: "v13.0"
                });
                resolve();
            };
        });
    },

    async loadFacebookSDK(d, s, id) {
        var js, fjs = d.getElementsByTagName(s)[0];
        if (d.getElementById(id)) {
            return;
        }
        js = d.createElement(s);
        js.id = id;
        js.src = "https://connect.facebook.net/en_US/sdk.js";
        fjs.parentNode.insertBefore(js, fjs);
    },
  },

}
</script>

