<script>
import Parse from 'parse';
import AppFooter from '@/components/footer'

export default {
  name: 'SSignUp',
  components: {
    AppFooter,
  },
  metaInfo: {
    title: 'S-Sign-Up - exported project',
    meta: [
      {
        property: 'og:title',
        content: 'S-Sign-Up - exported project',
      },
    ],
  },

  data(){
    return {
      ShowModal: false,
      ModalTab: "Step2",

      Username: '',
      ShopName: '',
      Email: '',
      Password: '',
      ConfirmPassword: '',
      VerificationFile: undefined,
      ContinueButtonDisabled: true,
      CheckIfVerified: false,
      Timer: undefined,
      SignUpType: '',
    }
  },

  methods:{
    signInPage(){
      this.$router.push({
          name: 's-sign-in',
      });
    },

    //FACEBOOK
    async signInWithFacebook() {
        await this.loadFacebookSDK(document, "script", "facebook-jssdk");
        await this.initFacebook();
        await window.FB.login(function(response) {
            var facebookNewUser = false;
            if (response.authResponse) {
                const authData = {
                    "id": response.authResponse.userID,
                    "access_token": response.authResponse.accessToken,
                    "expiration_date": response.authResponse.expiresIn,
                }
                window.FB.api('/me', function(data) {
                    const user = new Parse.User();
                    user.set("username", data.name);
                    var names = data.name.split(' ');
                    user.set("firstname", names[0]);
                    user.set("lastname", names[names.length - 1]);
                    user.set("AccountType", "Seller");
                    user.linkWith("facebook", { authData }).then(async (_obj)=>{
                        await Parse.User.logInWith('facebook', { authData });
                        window.location.pathname = "/s-sign-up"
                    });
                });
            }
            else {
                alert("User cancelled login or did not fully authorize.");
            }
        });
    },

    async initFacebook() {
        return new Promise((resolve, _reject) => {
            window.fbAsyncInit = function() {
                window.FB.init({
                    appId: "490657576400793", //You will need to change this
                    cookie: true, // This is important, it's not enabled by default
                    version: "v13.0"
                });
                resolve();
            };
        });
    },

    async loadFacebookSDK(d, s, id) {
        var js, fjs = d.getElementsByTagName(s)[0];
        if (d.getElementById(id)) {
            return;
        }
        js = d.createElement(s);
        js.id = id;
        js.src = "https://connect.facebook.net/en_US/sdk.js";
        fjs.parentNode.insertBefore(js, fjs);
    },
    
    async onImageSelected(e){
      var file = e.target.files[0];
      var parseFile = new Parse.File(file.name, file);
      await parseFile.save().then(()=> {
        this.VerificationFile = parseFile.url();
        this.ContinueButtonDisabled = false;
      });
    },

    stopVerification(){
      clearInterval(this.Timer);
    },

    async checkVerification(){
      if(!this.CheckIfVerified) return;
      console.log(Parse.User.current().id);
      var updated = JSON.parse(await Parse.Cloud.run("GetUpdatedUserData",{"UserID" : Parse.User.current().id}));
      if(updated.emailVerified){
        this.stopVerification();
        this.$router.push({
          name: 'sso',
        });
      }
    },

    gotoTab(tab){
      this.ModalTab = tab;
    },

    skipVerification(){
      this.ModalTab = "Step3";
    },

    uploadImage(){
      document.getElementById("uploadVerificationImage").click()
    },

    signUpEmail(){
      this.ShowModal = true;
      this.ModalTab = "Step2";
    },

    async signUp(){
      //check inputs
      if(this.Password != this.ConfirmPassword){
        alert("Password not the same as confirmation");
        return;
      }

      var param = {
        "ShopUsername" : this.Username,
        "ShopName" : this.ShopName,
        "ShopImage" : undefined,
        "ShopBanner" : undefined,
        "ShopVerificationFile" : this.VerificationFile,
      }

      //STOPPED HERE, TRY
      await Parse.Cloud.run("AddShop", param).then(async (res)=>{
        if(this.SignUpType != 'Facebook'){
          var user = new Parse.User();
          user.set("username", this.Username);
          user.set("password", this.Password);
          user.set("email", this.Email);
          user.set("AccountType", "Seller");
          user.set("AccountID", res);

          user.signUp().then(()=>{
            Parse.User.logIn(this.Username, this.Password).then(async ()=>{
              //await
              //this.gotoHomepage();
              this.ModalTab = "Step1";
              this.CheckIfVerified = true;
              this.Timer = setInterval(async ()=>{ this.checkVerification() }, 1000);
            });
          });
        }
        else{
          var params = {
            "AccountID" : res,
          }
          await Parse.User.current().set("AccountID", res);
          await Parse.User.current().save();
          await Parse.Cloud.run("EditUser", params);
          this.$router.push({
            name: 'sso',
          });
        }
      });
    },
  },

  beforeMount(){
    if(Parse.User.current() != undefined && Parse.User.current().get("AccountID") == undefined){
      //Need to check if google
      this.SignUpType = "Facebook";
      this.signUpEmail();
    }
    if(Parse.User.current() != undefined && Parse.User.current().get("AccountID") != undefined){
      this.$router.push({
          name: 'sso',
      });
    }
  },
}
</script>

