<template>
  <div v-if="ShowModal" id="myModal" class="modal">
    <!-- Modal content -->
    <div class="modal-content">
      <button @click="closeModal()" class="close">&times;</button>

      <template v-if="ModalTab === 'SetShippingFee'">
        <span>Shipping Fee: </span>
        <input v-model="ShippingFeeInput" type="number"> <br>
        <button @click="setOrderShippingFee()">Set Shipping Fee</button>
      </template>

      <template v-if="ModalTab === 'SetOrderToShipping'">
        <input v-model="ETA" type="date">
        <button @click="setOrderToShipping()">Set Order To Shipping</button>  
      </template>

      <template v-if="ModalTab === 'ViewGCash'">
        <span v-if="GCashReceipt == undefined">Order is not yet paid</span>
        <template v-if="GCashReceipt != undefined">
          Reference Number: {{GCashReceipt.ReferenceNumber}}
          <button @click="processPayment('approve')">Approve Payment</button><br>
          <button @click="processPayment('disapprove')">Disapprove Payment</button><br>
        </template>
      </template>

      <template v-if="ModalTab === 'EditSKU'">
        <span>Price: </span>
        <input v-model="SKUDataEdit.OriginalPrice" type="Number"><br>
        <span>Outgoing Stock: </span>
        <input v-model="SKUDataEdit.OutgoingStock" type="Number"><br>
        <span>Stock on hand: </span>
        <input v-model="SKUDataEdit.StockOnHand" type="Number"><br>
        <button @click="editSKU()">Edit</button><br>
        <button @click="closeModal()">Cancel</button><br>
        <!--Images edit soon-->
      </template>

    </div>
  </div>

  <div class="s-s-o-container">
    <s-header rootClassName="s-header-root-class-name"></s-header>
    <div class="s-s-o-s-o-b-o-d-y">
      <div class="s-s-o-seller-side-bar">
        <div class="s-s-o-shipment-orders">
          <span class="s-s-o-h1 button">Shipment &amp; Orders</span>
          <div class="s-s-o-s-o">
            <span @click="switchTab('shipment_and_orders','all')"
              :class="[currentMenu=='shipment_and_orders' && currentTab=='all' ? 's-s-o-s-o1 button' : 's-s-o-s-o2 button']">
              All</span>
            <span @click="switchTab('shipment_and_orders','pending')"
              :class="[currentMenu=='shipment_and_orders' && currentTab=='pending' ? 's-s-o-s-o1 button' : 's-s-o-s-o2 button']">Pending</span>
            <span @click="switchTab('shipment_and_orders','unpaid')"
              :class="[currentMenu=='shipment_and_orders' && currentTab=='unpaid' ? 's-s-o-s-o1 button' : 's-s-o-s-o2 button']">Unpaid</span>
            <span @click="switchTab('shipment_and_orders','toship')"
              :class="[currentMenu=='shipment_and_orders' && currentTab=='toship' ? 's-s-o-s-o1 button' : 's-s-o-s-o3 button']">To
              Ship</span>
            <span @click="switchTab('shipment_and_orders','shipping')"
              :class="[currentMenu=='shipment_and_orders' && currentTab=='shipping' ? 's-s-o-s-o1 button' : 's-s-o-s-o4 button']">Shipping</span>
            <span @click="switchTab('shipment_and_orders','completed')"
              :class="[currentMenu=='shipment_and_orders' && currentTab=='completed' ? 's-s-o-s-o1 button' : 's-s-o-s-o5 button']">Completed</span>
            <span @click="switchTab('shipment_and_orders','cancellation')"
              :class="[currentMenu=='shipment_and_orders' && currentTab=='cancellation' ? 's-s-o-s-o1 button' : 's-s-o-s-o6 button']">Cancellation</span>
          </div>
        </div>

        <div class="s-s-o-product">
          <span class="s-s-o-h2 button">Product</span>
          <div class="s-s-o-p-r">
            <span @click="addNewProduct()" class="s-s-o-p-r1 button">Add New Product</span>
            <span @click="switchTab('product', 'all')"
              :class="[currentMenu=='product' && currentTab=='all' ? 's-s-o-s-o1 button' : 's-s-o-p-r1 button']">All</span>
            <span @click="switchTab('product', 'live')"
              :class="[currentMenu=='product' && currentTab=='live' ? 's-s-o-s-o1 button' : 's-s-o-p-r3 button']">Live</span>
            <span @click="switchTab('product', 'soldout')"
              :class="[currentMenu=='product' && currentTab=='soldout' ? 's-s-o-s-o1 button' : 's-s-o-p-r4 button']">Sold
              Out</span>
            <span @click="switchTab('product', 'violation')"
              :class="[currentMenu=='product' && currentTab=='violation' ? 's-s-o-s-o1 button' : 's-s-o-p-r5 button']">Violation</span>
          </div>
        </div>

        <div class="s-s-o-discount-promotions">
          <span class="s-s-o-h3 button">Discount Promotions</span>
          <div class="s-s-o-d-p">
            <span class="s-s-o-d-p1 button">All</span>
            <span class="s-s-o-d-p2 button">Ongoing</span>
            <span class="s-s-o-d-p3 button">Upcoming</span>
            <span class="s-s-o-d-p4 button">Expired</span>
          </div>
        </div>

        <div class="s-s-o-income-details">
          <span class="s-s-o-h4 button">Income Details</span>
        </div>

        <div class="s-s-o-chat">
          <span @click="switchTab('chat_auto_reply', '')"
            :class="[currentMenu=='chat_auto_reply' ? 's-s-o-h5 button' : 's-s-o-h5 button']">Chat Auto Reply</span>
        </div>

        <div class="s-s-o-shop">
          <span class="s-s-o-h6 button">Shop</span>
          <div class="s-s-o-s-h">
            <span @click="switchTab('shop_rating', '')"
              :class="[currentMenu=='shop_rating' ? 's-s-o-s-o1 button' : 's-s-o-s-h1 button']">Rating</span>
            <span @click="switchTab('shop_profile_and_decoration', '')"
              :class="[currentMenu=='shop_profile_and_decoration' ? 's-s-o-s-o1 button' : 's-s-o-s-h2 button']">Profile
              &amp;
              Decoration</span>
          </div>
        </div>

        <div class="s-s-o-settings">
          <span class="s-s-o-h7 button">Settings</span>
          <div class="s-s-o-s-e">
            <span @click="switchTab('settings_my_address', '')"
              :class="[currentMenu=='settings_my_address' ? 's-s-o-s-o1 button' : 's-s-o-s-e1 button']">My
              Addresses</span>
            <span @click="switchTab('settings_account', '')"
              :class="[currentMenu=='settings_account' ? 's-s-o-s-o1 button' : 's-s-o-s-e2 button']">Account</span>
          </div>
        </div>
      </div>

      <!--SHIPMENTS AND ORDERS-->
      <div v-if="currentMenu=='shipment_and_orders'" class="s-s-o-sobody1">
        <div class="s-s-o-navigation">
          <button class="s-s-o-n0 button">
            <span class="s-s-o-text">All</span>
            <svg viewBox="0 0 1024 1024" class="s-s-o-icon">
              <path d="M298 426h428l-214 214z"></path>
            </svg>
          </button>

          <span @click="switchTab('shipment_and_orders','all')"
