          <span @click="switchTab('shipment_and_orders','shipping')"
            :class="[currentTab=='shipping' ? 's-s-o-n1 button' : 's-s-o-n2 button']">Shipping</span>
          <span @click="switchTab('shipment_and_orders','completed')"
            :class="[currentTab=='completed' ? 's-s-o-n1 button' : 's-s-o-n2 button']">Completed</span>
          <span @click="switchTab('shipment_and_orders','cancellation')"
            :class="[currentTab=='cancellation' ? 's-s-o-n1 button' : 's-s-o-n2 button']">Cancellation</span>
        </div>

        <div class="s-s-o-m-a-i-n-b-o-d-y">
          
          <span class="s-s-o-o-r-d-e-r-c-o-u-n-t">0 Orders</span>
          <!--CHECKPOINT-->
          <div class="s-s-o-s-o-t-a-b-l-e">
            <span class="s-s-o-t-h1">Customer Name</span>
            <span class="s-s-o-t-h2">Order ID</span>
            <span class="s-s-o-t-h3">Product(s)</span>
            <span class="s-s-o-t-h4">Order Total</span>
            <span class="s-s-o-t-h5">Status</span>
            <span class="s-s-o-t-h6">ETA</span>
            <span class="s-s-o-t-h7">Delivery Method</span>
            <span class="s-s-o-t-h8">Actions</span>

            <template v-for="(order, i) in orderQuery()" :key="order" >
              <span class="s-s-o-r1">
                <template  v-if="order.CustomerData != undefined">
                  {{order.CustomerData.FirstName}} {{order.CustomerData.LastName}}
                </template>
              </span>
              <span class="s-s-o-r2">{{order.objectId}}</span>
              <span class="s-s-o-r3">
                <span v-if="order.SKUData != undefined" >{{order.SKUData.SKU}}</span>
              </span>
              <span class="s-s-o-r4">
                <template v-if="order.PaymentDetailData != undefined">
                  {{order.PaymentDetailData.MerchandiseSubtotal}}
                </template>
              </span>
              <span class="s-s-o-r5">
                <template v-if="order.OrderStatusData != undefined" >
                  {{order.OrderStatusData.OrderStatus}}
                </template>
              </span>
              <span class="s-s-o-r6">
                <template v-if="order.OrderStatusData != undefined" >
                  {{order.OrderStatusData.ETA}}
                </template>
              </span>
              <span class="s-s-o-r7">Standard Delivery</span>
              <!--ACTIONS-->
              <div class="s-s-o-r8">
                <template v-if="order.OrderStatusData != undefined">
                  <span v-if="order.OrderStatusData.OrderStatus === 'Pending'" @click="orderAction('SetShippingFee', i)" class="s-s-o-a1 button">Set Shipping Fee</span>
                  <!--NEW-->
                  <span v-if="order.OrderStatusData.OrderStatus === 'ToShip'" @click="orderAction('SetOrderToShipping', i)" class="s-s-o-a1 button">Set Order to Shipping</span>
                
                  <template v-if="PaymentMethods[order.PaymentMethodID] != undefined">
                    <span v-if="order.OrderStatusData.OrderStatus === 'Shipping' && PaymentMethods[order.PaymentMethodID].PaymentMethod === 'Cash on Delivery' && order.PaymentDetailData.PaymentStatus === 'Unpaid'" @click="setOrderAsPaid(i)" class="s-s-o-a1 button">
                      Set order as paid
                    </span>
                    <span v-if="PaymentMethods[order.PaymentMethodID].PaymentMethod === 'GCash' && order.OrderStatusData.OrderStatus === 'Unpaid'" @click="orderAction('ViewGCash', i)" class="s-s-o-a1 button">View Gcash Receipt</span>
                  </template>
                
                  <span v-if="order.OrderStatusData.OrderStatus != 'Cancelled' && order.OrderStatusData.OrderStatus != 'Completed'" @click="cancelOrder(i)" class="s-s-o-a2 button">
                    <span>Cancel</span>
                    <br />
                  </span>

                </template>
              </div>
            </template>
          </div>

        </div>

