      </div>

      <!--PRODUCTS MENU-->
      <div v-if="currentMenu=='product'" class="s-p-product-body">
        <div v-if="currentTab != 'violation'">
          <div class="s-p-sort-filter">
            <div class="s-p-sort">
              <div class="s-p-s-b-a-r">
                <select v-model="CategorySearchType" @change="categoryOnChange()" class="s-p-s-f1">
                  <option value="All">All</option>
                  <template v-for="category in Categories" :key="category">
                    <option v-bind:value="category.CategoryName">{{category.CategoryName}}</option>
                  </template>
                </select>
                <select v-model="ProductSearchType" class="s-p-s-f2">
                  <option value="" hidden>Type</option>
                  <option value="ProductName">Product Name</option>
                  <option value="SKU">SKU</option>
                </select>
              </div>
              <div class="s-p-search-bar">
                <input v-model="ProductSearch" type="text" class="s-p-textinput input" />
                <svg viewBox="0 0 1024 1024" class="s-p-searchheader">
                  <path
                    d="M406 598q80 0 136-56t56-136-56-136-136-56-136 56-56 136 56 136 136 56zM662 598l212 212-64 64-212-212v-34l-12-12q-76 66-180 66-116 0-197-80t-81-196 81-197 197-81 196 81 80 197q0 42-20 95t-46 85l12 12h34z">
                  </path>
                </svg>
              </div>
            </div>
            <div class="s-p-filter">
              <div class="s-p-f-b-a-r">
                <div class="s-p-f-b-a-r1">
                  <span class="s-p-f1">Stock</span>
                  <input type="text" placeholder="min" class="s-p-f2 input" />
                  <span class="s-p-f3"><span>-</span></span>
                  <input type="text" placeholder="max" class="s-p-f4 input" />
                </div>
                <div class="s-p-f-b-a-r2">
                  <span class="s-p-f5">Sales</span>
                  <input type="text" placeholder="min" class="s-p-f6 input" />
                  <span class="s-p-f7"><span>-</span></span>
                  <input type="text" placeholder="max" class="s-p-f8 input" />
                </div>
              </div>
              <div class="s-p-s-f-b-u-t-t-o-n">
                <button class="s-p-s-f11 button">Search</button>
                <button class="s-p-s-f21 button">Reset</button>
              </div>
            </div>
          </div>
          <div class="s-p-m-a-i-n-b-o-d-y">
            <div class="s-p-navigation">
              <span @click="switchTab('product', 'all')"
                :class="[currentTab=='all' ? 's-p-n1 button' : 's-p-n2 button']">All</span>
              <span @click="switchTab('product', 'live')"
                :class="[currentTab=='live' ? 's-p-n1 button' : 's-p-n2 button']">Live</span>
              <span @click="switchTab('product', 'soldout')"
                :class="[currentTab=='soldout' ? 's-p-n1 button' : 's-p-n2 button']">Sold Out</span>
              <span @click="switchTab('product', 'suspended')"
                :class="[currentTab=='suspended' ? 's-p-n1 button' : 's-p-n2 button']">Suspended</span>
              <span @click="switchTab('product', 'deleted')"
                :class="[currentTab=='deleted' ? 's-p-n1 button' : 's-p-n2 button']">Deleted</span>
              <span @click="switchTab('product', 'violation')"
                :class="[currentTab=='violation' ? 's-p-n1 button' : 's-p-n2 button']">Violation</span>
            </div>

            <div class="s-p-p-r-o-d-u-c-t-c-o-u-n-t">
              <span class="s-p-product-count1">{{getSKUsLength(currentTab)}} Products</span>
              <button @click="addNewProduct()" v-if="currentTab=='all'" class="s-p-new-product button">Add a New
                Product</button>
            </div>

            <div class="s-p-p-t-a-b-l-e">
              <span class="s-p-t-h1">Product Name</span>
              <span class="s-p-t-h2">SKU</span>
              <span class="s-p-t-h3">Variations</span>
              <span class="s-p-t-h4">Price</span>
              <span class="s-p-t-h5">Outgoing Stock</span>
              <span class="s-p-t-h6">Stock on Hand</span>
              <span class="s-p-t-h7">Sales</span>
              <span class="s-p-t-h8">Promo</span>
              <span class="s-p-t-h9">Status</span>
              <span class="s-p-t-h10">Action</span>

              <template v-for="(sku, i) in categoryQuery()" :key="sku">
                <template v-if="currentTab == formatStatus(sku.Status) || currentTab == 'all'">
                  <span class="s-p-r1">
                    {{getProductNameFromSKU(sku.SKU)}}
                  </span>
                  <span class="s-p-r2">{{sku.SKU}}</span>
                  <span class="s-p-r3">
                    <template v-for="variation of getVariationsFromSKU(sku.SKU)">
                      {{variation}} {{}}
                    </template>
                  </span>
                  <span class="s-p-r4">{{sku.OriginalPrice}}</span>
                  <span class="s-p-r5">{{sku.OutgoingStock}}</span>
                  <span class="s-p-r6">{{sku.StockOnHand}}</span>
                  <span class="s-p-r7">Sales</span>
                  <span class="s-p-r8">Promo</span>
                  <span class="s-p-r9">{{sku.Status}}</span>
                  <span class="s-p-r10">
                    <template v-if="sku.Status == 'Live' || sku.Status == 'Sold Out'">
                      <span @click="showEditSKU(i)" class="s-s-o-a2 button">
                        Edit
                      </span><br>
                    </template>

                    <template v-if="sku.Status == 'Suspended'">
                      <span @click="unsuspendSKU(i)" class="s-s-o-a2 button">
                        Unsuspend
                      </span><br>
                    </template>

                    <template v-if="sku.Status !='Suspended' && sku.Status != 'Deleted'">
                      <span @click="suspendSKU(i)" class="s-s-o-a2 button">
                        Suspend
                      </span><br>
                    </template>

                    <template v-if="sku.Status != 'Deleted'">
                      <span @click="deleteSKU(i)" class="s-s-o-a2 button">
                        Delete
                      </span>
                    </template>

                  </span>
                </template>
              </template>

            </div>
          </div>
        </div>

        <div v-if="currentTab == 'violation'" class="s-p-v-p-v-b-o-d-y">
          <div class="s-p-v-p-v-search">
            <div class="s-p-v-p-v-s">
              <div class="s-p-v-p-v-s1">
                <select class="s-p-v-p-v-s1sort">
                  <option value="Option 1">Product Name</option>
                </select>
              </div>
              <div class="s-p-v-p-v-s2">
                <input type="text" class="s-p-v-p-v-s2input input" />
                <svg viewBox="0 0 1024 1024" class="s-p-v-p-v-s2icon">
                  <path
                    d="M406 598q80 0 136-56t56-136-56-136-136-56-136 56-56 136 56 136 136 56zM662 598l212 212-64 64-212-212v-34l-12-12q-76 66-180 66-116 0-197-80t-81-196 81-197 197-81 196 81 80 197q0 42-20 95t-46 85l12 12h34z">
                  </path>
                </svg>
              </div>
            </div>
            <div class="s-p-v-p-v-buttons">
              <button class="s-p-v-p-v-b1 button">Search</button>
              <button class="s-p-v-p-v-b2 button">Reset</button>
            </div>
          </div>
          <div class="s-p-v-m-a-i-n-b-o-d-y">
