<script>
import Parse from 'parse';
import SHeader from '@/components/s-header'

export default {
  name: 'SSO',
  components: {
    SHeader,
  },
  metaInfo: {
    title: 'SSO - exported project',
    meta: [
      {
        property: 'og:title',
        content: 'SSO - exported project',
      },
    ],
  },

  beforeMount() {
    this.loadData();
    if (this.$route.query.Tab != undefined) {
      this.switchTab(this.$route.query.Tab, "");
    }
    console.log(Parse.User.current().get("AccountID"));
  },

  data() {
    return {
      currentTab: 'all',
      currentMenu: 'product',
      //Others
      PaymentMethods: {},
      ShowModal: false,
      ModalTab: '',
      OrderIndex: '',
      ShippingFeeInput: 0,
      GCashReceipt: undefined,
      ETA: '',

      //Order
      Orders: [],

      //Product
      SKUs: [],
      Categories: [],
      NameSearchType: "ProductName",
      CategorySearchType: "All",
      StockSearch: ["", ""],
      SalesSearch: ["", ""],
      ProductSearch: "",
      ProductSearchType: "",

      SKUDataEdit: {},

      //Shop 
      ShopName: "OTOP HUB",
      ShopDescription: "Yeah",
      ShopRatings: [],
      RatingTab: 'All',
      ShopCoverPhoto: 'https://play.teleporthq.io/static/svg/default-img.svg',
      ShopImage: 'https://play.teleporthq.io/static/svg/default-img.svg',

      //Account
      Addresses: [],
      Username: "",
      NewUsername: "",
      Phone: Parse.User.current().get("phone"),
      NewPhone: Parse.User.current().get("phone"),
      Email: Parse.User.current().get("email"),
      NewEmail: Parse.User.current().get("email"),
      AuthData: Parse.User.current().get("authData"),
    }
  },

  methods: {
    //Others
    async logOut(){
      await Parse.User.logOut();
      this.$router.push({
        name: 's-sign-in',
      });
    },

    async onCoverImageSelected(e){
      var file = e.target.files[0];
      var parseFile = new Parse.File(file.name, file);
      await parseFile.save().then(()=> {
        this.ShopCoverPhoto = parseFile.url();
      });
    },

    async onProfileImageSelected(e){
      var file = e.target.files[0];
      var parseFile = new Parse.File(file.name, file);
      await parseFile.save().then(()=> {
        this.ShopImage = parseFile.url();
      });
    },

    editCoverPhoto(){
      document.getElementById("uploadCoverImage").click();
    },

    editProfilePhoto(){
      document.getElementById("uploadProfileImage").click();
    },

    resetPassword(){
      Parse.User.requestPasswordReset(this.Email).then(()=>{
        alert("An reset email is sent!");
      });
    },

    //Shop ratings
    switchRatingTab(tab){
      this.RatingTab = tab;
    },

    getShopAverageRating(){
      var total = 0;
      for(var rating of this.ShopRatings){
        total += rating.Rating;
      }
      if(total == 0) return total;
      return total / this.ShopRatings.length;
    },

    async pushNotification(customerID, header, title, subtitle, image){
      var params = {
        "CustomerID" : customerID,
        "Header" : header,
        "Title" : title,
        "Subtitle" : subtitle,
        "Image" : image,
      }
      await Parse.Cloud.run("AddNotification", params);
    },
    
    //Order funcs
    /*
      SHOULD NOT BE THIS.ORDERS // QUERY ORDERS!!!!!
      DONE, checking
    */

    async setOrderAsPaid(index){
      this.OrderIndex = index;
      var orderPaymentDetail = this.orderQuery()[this.OrderIndex].PaymentDetailData;
      orderPaymentDetail["PaymentDetailID"] = orderPaymentDetail.objectId;
      orderPaymentDetail["PaymentStatus"] = "Paid";
      await Parse.Cloud.run("EditPaymentDetail", orderPaymentDetail);
    },

    async cancelOrder(index){
      this.OrderIndex = index;
      var orderStatus = this.orderQuery()[this.OrderIndex].OrderStatusData;
      orderStatus["OrderStatusID"] = orderStatus.objectId;
      orderStatus["OrderStatus"] = "Cancelled";
      await Parse.Cloud.run("EditOrderStatus", orderStatus);
      this.pushNotification(this.orderQuery()[this.OrderIndex].CustomerID, "Order Cancelled", "Order ID:"+orderStatus.OrderID, "Date today", "");
    },

    async orderAction(type, index){
      this.ModalTab = type;
      this.OrderIndex = index;
      this.ShowModal = true;

      //CHECKPOINT2
      if(type == 'ViewGCash'){
        //Check if there is Gcash Receipt
        var tmp = await Parse.Cloud.run("GetOrderGcash", {"OrderID" : this.orderQuery()[this.OrderIndex].objectId});
        console.log(this.orderQuery()[this.OrderIndex].objectId);
        if(tmp != undefined){
          this.GCashReceipt = JSON.parse(tmp);
        }
      }
    },

    closeModal(){
      this.ShowModal = false;
    },

    async processPayment(type){
      var orderPaymentDetail = this.orderQuery()[this.OrderIndex].PaymentDetailData;
      orderPaymentDetail["PaymentDetailID"] = orderPaymentDetail.objectId;

      if(type == 'approve'){
        orderPaymentDetail["PaymentStatus"] = "Paid";
        //Set status to shipping
        var orderStatus = this.orderQuery()[this.OrderIndex].OrderStatusData;
        orderStatus["OrderStatusID"] = orderStatus.objectId;
        orderStatus["OrderStatus"] = "Shipping";
        await Parse.Cloud.run("EditOrderStatus", orderStatus);
      }
      else if(type == 'disapprove'){
        orderPaymentDetail["PaymentStatus"] = "Disapproved";
        //Set gcash status to closed
        var params = {
          "GcashID" : this.GCashReceipt.objectId,
          "Status" : "Closed",
        }
        await Parse.Cloud.run("EditGcash", params);
        this.GCashReceipt = undefined;
      }
      await Parse.Cloud.run("EditPaymentDetail", orderPaymentDetail);
      this.closeModal();
      alert("Payment Processed!");
    },

    async setOrderToShipping(){
      //SKU STOCK EDIT
      var skuData = this.orderQuery()[this.OrderIndex].SKUData;
      skuData["SKUID"] = skuData.objectId

      if(skuData.StockOnHand <= 0){
        alert("Please Restock First!");
        return;
      }

      skuData["StockOnHand"] -= 1;
      if(skuData["StockOnHand"] <= 0){
        skuData["Status"] = "Sold Out";
      }
      await Parse.Cloud.run("EditSKU", skuData);

      var orderStatus = this.orderQuery()[this.OrderIndex].OrderStatusData;
      orderStatus["OrderStatusID"] = orderStatus.objectId;
      orderStatus["OrderStatus"] = "Shipping";
      orderStatus["ETA"]= this.ETA;
      await Parse.Cloud.run("EditOrderStatus", orderStatus);
      this.closeModal();
      this.pushNotification(this.orderQuery()[this.OrderIndex].CustomerID, "Order Shipped!", "Order ID:"+orderStatus.OrderID, "Date today", "");
      alert("Set order as shipping");
    },

    async setOrderShippingFee(){
      var orderPaymentDetail = this.orderQuery()[this.OrderIndex].PaymentDetailData;
      orderPaymentDetail["ShippingTotal"] = this.ShippingFeeInput;
      orderPaymentDetail["TotalPayment"] = orderPaymentDetail.TotalPayment + this.ShippingFeeInput;
      orderPaymentDetail["PaymentDetailID"] = orderPaymentDetail.objectId;

      //If gcash = unpaid
      //If cod = to ship
      var payment = this.PaymentMethods[this.orderQuery()[this.OrderIndex].PaymentMethodID].PaymentMethod
      var orderStatus = this.orderQuery()[this.OrderIndex].OrderStatusData;
      orderStatus["OrderStatusID"] = orderStatus.objectId;

      if(payment == "GCash"){
        orderStatus["OrderStatus"] = "Unpaid";
        //orderPaymentDetail["PaymentStatus"] = "Pending";
      }
      else{
        orderStatus["OrderStatus"] = "ToShip";
      }

      await Parse.Cloud.run("EditPaymentDetail", orderPaymentDetail);
      await Parse.Cloud.run("EditOrderStatus", orderStatus);
      this.pushNotification(this.orderQuery()[this.OrderIndex].CustomerID, "Order is approved!", "Order ID:"+orderStatus.OrderID, "Date today", "");
      this.closeModal();
      alert("Added Shipping Fee");
    },

    //Product functions

    async suspendSKU(index){
      this.categoryQuery()[index]["Status"] = "Suspended";
      this.categoryQuery()[index]["SKUID"] = this.categoryQuery()[index].objectId;
      await Parse.Cloud.run("EditSKU", this.categoryQuery()[index]);
    },

    async unsuspendSKU(index){
      this.categoryQuery()[index]["Status"] = "Live";
      if(this.categoryQuery()[index]["StockOnHand"] <= 0) this.categoryQuery()[index]["Status"] = "Sold Out";
      this.categoryQuery()[index]["SKUID"] = this.categoryQuery()[index].objectId;
      await Parse.Cloud.run("EditSKU", this.categoryQuery()[index]);
    },

    async deleteSKU(index){
      this.categoryQuery()[index]["Status"] = "Deleted";
      this.categoryQuery()[index]["SKUID"] = this.categoryQuery()[index].objectId;
      await Parse.Cloud.run("EditSKU", this.categoryQuery()[index]);
    },

    async showEditSKU(index){
      this.ModalTab = "EditSKU";
      this.OrderIndex = index;
      this.ShowModal = true;
      this.SKUDataEdit = this.categoryQuery()[index];
    },

    async editSKU(){
      this.SKUDataEdit["SKUID"] = this.categoryQuery()[this.OrderIndex].objectId;
      this.SKUDataEdit["StockOnHand"] = Number(this.SKUDataEdit["StockOnHand"]);
      this.SKUDataEdit["Price"] = Number(this.SKUDataEdit["Price"]);
      this.SKUDataEdit["OutgoingStock"] = Number(this.SKUDataEdit["OutgoingStock"]);
      console.log(this.SKUDataEdit);
      //this.categoryQuery()[this.OrderIndex] = this.SKUDataEdit;
      await Parse.Cloud.run("EditSKU", this.SKUDataEdit);
      alert("EDITED SKU!");
      this.ShowModal = false;
    },

    formatStatus(stat){
      return stat.replace(/\s+/g, '').toLowerCase();
    },

    getSKUsLength(stat){
      var i = 0;
      if(stat == "all") return this.SKUs.length;
      if(stat == "live") stat = "Live";
      else if(stat == "soldout") stat = "Sold Out";
      else if(stat == "violation") stat = "Violation";
      for(var key in this.SKUs){
        if(this.SKUs[key].Status == stat) i++;
      }
      return i;
    },

    getProductNameFromSKU(sku){
      return sku.split("_")[0];
    },

    getVariationsFromSKU(sku){
      var arr = sku.split("_");
      arr.shift();
      return arr;
    },

    categoryOnChange(){
      console.log("Category on change:",this.categoryQuery());
    },

    //TODO recheck to all types
    orderQuery(){
      var orders = [];
      if(this.currentTab != 'all'){
        var s = this.currentTab;
        for(var ord of this.Orders){
          console.log(s[0].toUpperCase() + s.slice(1));
          if(ord.OrderStatusData.OrderStatus == s[0].toUpperCase() + s.slice(1)){
            orders.push(ord);
          }
          //for deleted status
          else if((ord.OrderStatusData.OrderStatus == 'Deleted' && s == 'suspended') 
          || (ord.OrderStatusData.OrderStatus == 'Cancelled' && s == 'cancellation')
          || (ord.OrderStatusData.OrderStatus == 'ToShip' && s == 'To Ship')){
            orders.push(ord);
          }
        }
      }
      else{
        orders = this.Orders;
      }
      return orders;
    },

    //TODO add search on stock and sales
    categoryQuery(){
      var arr = [];
      if(this.CategorySearchType == "All"){
        return this.productQuery();
      }

      for(var sku of this.productQuery()){
        if(this.CategorySearchType == sku.SKUData.ProductData.CategoryData.CategoryName){
          arr.push(sku);
        }
      }

      return arr;
    },

    productQuery(){
      if(this.ProductSearch !== ""){
          return this.SKUs.filter((sku)=>{
              var value;
              switch(this.ProductSearchType) {
                  case "ProductName":
                      value = this.getProductNameFromSKU(sku.SKU);
                      break;
                  case "SKU":
                      value = sku.SKU;
                      break;
                  default:
                      // code block
              }
              return this.ProductSearch.toLowerCase().split(' ').every(v => value.toLowerCase().includes(v))
          });
      }
      else{
          return this.SKUs;
      }
    },

    addNewProduct(){
      this.$router.push({
        name: 'addproduct',
      });
    },

    //Shop FUnctions
    async editShop(){
      var params = {
        "ShopID" : Parse.User.current().get("AccountID"),
        "ShopName" : this.ShopName,
        "ShopDescription" : this.ShopDescription,
        "ShopBanner" : this.ShopCoverPhoto,
        "ShopImage" : this.ShopImage,
      }
      await Parse.Cloud.run("EditShop", params);
      alert("Edited Shop!");
    },

    //Account Functions
    addNewAddress(){
      this.$router.push({
        name: 's-setup-shop-info',
        query: {
          ShopAddressID: "new",
        }
      });
    },

    editAddress(id){
      this.$router.push({
        name: 's-setup-shop-info',
        query: {
          ShopAddressID: id,
        }
      });
    },

    async editUserDetails(type){
      var params = {};
      if(type == "phone"){
        params["phone"] = this.NewPhone;
        await Parse.User.current().set("phone", this.NewPhone);
        await Parse.User.current().save();
        await Parse.Cloud.run("EditUser", params);
        alert("Phone Number Saved!");
      }
      else if(type == "email"){
        params["email"] = this.NewEmail;
        await Parse.User.current().set("email", this.NewEmail);
        await Parse.User.current().save();
        await Parse.Cloud.run("EditUser", params);
        alert("Email Saved!")
      }
      //CHECK USERNAME if valid
      else if(type == "username"){
        params = {
          "ShopID" : Parse.User.current().get("AccountID"),
          "ShopUsername" : this.NewUsername,
        }
        await Parse.Cloud.run("EditShop", params);
        alert("Username Saved!")
      }
    },

    async cancelUserDetails(type){
      if(type == "phone"){
        this.NewPhone = this.Phone;
      }
      else if(type == "email"){
        this.NewEmail = this.Email;
      }
      else if(type == "username"){
        this.NewUsername = this.Username;
      }
    },

    //Other Functions
    async loadData(){
      var params = {"ShopID" : Parse.User.current().get("AccountID")};
      
      //Products Data
      this.SKUs = JSON.parse(await Parse.Cloud.run("GetShopSKUs", params));
      this.Categories = JSON.parse(await Parse.Cloud.run("GetCategories"));
      for await(var sku of this.SKUs){
        params = {"SKUID" : sku.objectId};
        sku["SKUData"] = JSON.parse(await Parse.Cloud.run("GetSKUData", params));
        sku["ModifiedProductName"] = this.getProductNameFromSKU(sku.SKU);
        sku["ModifiedVariations"] = this.getVariationsFromSKU(sku.SKU);
      }

      //Orders
      params = {"ShopID" : Parse.User.current().get("AccountID")};
      this.Orders = JSON.parse(await Parse.Cloud.run("GetShopOrders", params));
      for await (var order of this.Orders){
        //CHECKPOINT
        order["PaymentDetailData"] = JSON.parse(await Parse.Cloud.run("GetPaymentDetailByOrderID", {"OrderID" : order.objectId}));
        order["OrderStatusData"] = JSON.parse(await Parse.Cloud.run("GetOrderStatusByOrderID", {"OrderID" : order.objectId}));
        order["CustomerData"] = JSON.parse(await Parse.Cloud.run("GetCustomerData", {"CustomerID" : order.CustomerID}));
        order["SKUData"] = this.SKUs.filter(sku => {return sku.objectId === order.SKUID})[0];
      }
      console.log(this.Orders);
      //alert("DONE LOADING ORDERS AND SKU!");

      //Payment Methods
      var tmp = JSON.parse(await Parse.Cloud.run("GetPaymentMethods"));
      for(var payment of tmp){
        this.PaymentMethods[payment.objectId] = payment;
      }

      //Shop Data
      params = {"ShopID" : Parse.User.current().get("AccountID")};
      var shopData = JSON.parse(await Parse.Cloud.run("GetShopData", params));
      this.ShopName = shopData.ShopName;
      this.ShopDescription = shopData.ShopDescription;
      if(shopData.ShopBanner != undefined){
        this.ShopCoverPhoto = shopData.ShopBanner;
      }
      if(shopData.ShopImage != undefined){
        this.ShopImage = shopData.ShopImage;
      }
      //My Addresses
      this.Addresses = JSON.parse(await Parse.Cloud.run("GetShopAddresses", params));
      //Account Data
      this.Username = shopData.ShopUsername;
      this.NewUsername = this.Username;
      //ratings
      this.ShopRatings = JSON.parse(await Parse.Cloud.run("GetShopProductRatings", params));
      for await (var rating of this.ShopRatings){
        rating["CustomerData"] = JSON.parse(await Parse.Cloud.run("GetCustomerData", {"CustomerID" : rating.CustomerID}));
        rating["SKUData"] = this.SKUs.filter(sku => {return sku.objectId === rating.SKUID})[0];
      }
    },

    switchTab(menu, tab){
      this.currentMenu = menu;
      this.currentTab = tab;
    },

    gotoPage(name){
      this.$router.push({
        name: name,
      });
    },

  }
}
</script>

