<template>
  <div class="c-homepage-top-container">
    <c-header rootClassName="c-header-root-class-name9"></c-header>
    <div class="c-homepage-top-categories">
      <button class="c-homepage-top-all button">All</button>

      <template v-for="category in Categories" :key="category">
        <button @click="switchCatgeory(category.objectId)"
          class="c-homepage-top-processed-food button">{{category.CategoryName}}
        </button>
      </template>

    </div>

    <div class="c-homepage-top-container1">
      <span class="c-homepage-top-discover">
        <span class="c-homepage-top-text">
          <template v-if="AllType=='TopSelling'">Top Selling</template>
          <template v-if="AllType=='DailyDiscover'">Daily Discover</template>
        </span>
        <br />
      </span>

      <div class="c-homepage-top-date">
        <button class="c-homepage-top-year button">Y</button>
        <button class="c-homepage-top-month button">M</button>
        <button class="c-homepage-top-week button">W</button>
        <button class="c-homepage-top-day button">D</button>
      </div>

    </div>
    <div class="c-homepage-top-container2">
      <div class="c-homepage-top-gallery">

        <template v-for="(arr, i) in ProductMatrix" :key="arr">
          <template v-if="i == TabIndex">
            <div v-for="prod in arr" :key="prod" class="c-homepage-top-container3 button">
              <img :src="[prod.ImagePreview[0] == '' ? 'https://play.teleporthq.io/static/svg/default-img.svg' : prod.ImagePreview[0]]" class="c-homepage-top-t-s-pimg1" />
              <span class="c-homepage-top-text2">
                <span class="c-homepage-top-text3">
                  <span>{{prod.ProductName}}</span>
                  <br />
                  <span></span>
                </span>
              </span>
              <span class="c-homepage-top-text7">Php {{getLowestPrice(prod.SKUs)}}</span>
              <span class="c-homepage-top-text8">{{prod.Sold}} Sold</span>
            </div>
          </template>
        </template>

        <div v-if="ProductMatrix.length > 1">
          <template v-for="(arr,i) in ProductMatrix" :key="arr">
            <button @click="switchTabIndex(i)">{{i+1}}</button> <span>{{ }}</span>
          </template>
        </div>

      </div>
    </div>
    <app-footer rootClassName="footer-root-class-name9"></app-footer>
  </div>
</template>

<script>
import Parse from 'parse';
import CHeader from '@/components/c-header'
import AppFooter from '@/components/footer'

export default {
  name: 'CHomepageTop',
  props: {},
  components: {
    CHeader,
    AppFooter,
  },

  data() {
    return {
      Products: [],
      ProductMatrix: [],
      TabIndex: 0,

      AllType: '',
      CategoryID: '',
      Categories: [],
    }
  },

  methods: {
    switchCatgeory(id) {
      this.$router.push({
        name: 'c-homepage-category',
        query: {
          CategoryID: id,
        }
      })
    },

    switchTabIndex(index) {
      this.TabIndex = index
    },

    getLowestPrice(arr) {
      return Math.min(...arr.map(item => item.OriginalPrice))
    },

    splitProducts() {
      var arr = [];
      for (const element of this.Products) {
        if (arr.length >= 20) {
          this.ProductMatrix.push(arr);
          arr = [];
        }
        arr.push(element);
      }
      this.ProductMatrix.push(arr);
    },

    async getProducts(type) {
      var tmp;
      if (type != undefined) {
        if (type === "TopSelling") {
          tmp = JSON.parse(await Parse.Cloud.run("GetTopProducts", { "Limit": 100 }));
        }
        else if (type === "DailyDiscover") {
          //TODO: Randomize this
          tmp = JSON.parse(await Parse.Cloud.run("GetProducts", { "Limit": 100 }));
        }
      }

      for await (var prod of tmp) {
        prod["SKUs"] = JSON.parse(await Parse.Cloud.run("GetProductSKUs", { "ProductID": prod.objectId }));
      }

      //TESTING
      /*
      var tmp2 = [];
      for (const element of tmp) {
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
      }
      tmp = tmp2;
      */
      //End of testing

      this.Products = tmp;
      this.splitProducts();
    },

    async getCategories() {
      this.Categories = JSON.parse(await Parse.Cloud.run("GetCategories"));
    },
  },

  beforeMount() {
    //get query
    this.AllType = this.$route.query.Type;
    this.getProducts(this.$route.query.Type);
    this.getCategories();
    console.log("TEST");
  },
}
</script>

