              <div class="c-store-container05">
                <span class="c-store-shop-name">
                  <span>
                    {{ShopData.ShopName}}
                  </span>
                </span>
                <button class="c-store-messagebuttonstore button">
                  <span class="c-store-message">
                    <span>Message</span>
                    <span></span>
                  </span>
                </button>
              </div>
            </div>
            <div class="c-store-aboutshop">
              <span class="c-store-shopinfo">ABOUT THIS SHOP</span>
              <div class="c-store-container06">
                <div class="c-store-p-c-r">
                  <div class="c-store-container07">
                    <span class="c-store-products">Products:</span>
                    <span class="c-store-ratingnum">{{ShopProducts.length}}</span>
                  </div>
                  <div class="c-store-container08">
                    <span class="c-store-products1">Rating:</span>
                    <span class="c-store-ratingnum1">{{getShopAverageRating()}} ({{ShopRatings.length}} Rating)</span>
                  </div>
                </div>
                <div class="c-store-container09">
                  <span class="c-store-textarea textarea">{{ShopData.ShopDescription}}</span>
                </div>
              </div>
            </div>
          </div>

          <div class="c-store-categories">
            <button @click="switchTab('home')"
              :class="[currentTab == 'home' ? 'c-store-p-fstore button' : 'c-store-p-fstore1 button']">Home</button>
            <button @click="switchTab('all')"
              :class="[currentTab == 'all' ? 'c-store-p-fstore button' : 'c-store-p-fstore1 button']">All
              Products</button>
            <template v-for="category in Categories" :key="category">
              <button @click="switchTab(category.CategoryName, category.objectId)"
                :class="[currentTab == category.CategoryName ? 'c-store-p-fstore button' : 'c-store-p-fstore1 button']">{{category.CategoryName}}</button>
            </template>
          </div>
        </div>
      </div>

      <div class="c-store-top-selling">
        <div class="c-store-container10">
          <span class="c-store-text04">
            <template v-if="currentTab == 'home'">Top Selling Products</template>
            <template v-if="currentTab == 'all'">All Products</template>
            <template v-if="currentTab != 'home' && currentTab != 'all'">{{currentTab}}</template>
          </span>
          <div class="c-store-container11">
            <span class="c-store-text05">Sort by:</span>
            <select @change="sortProducts($event)" class="c-store-select">
              <option value="Latest">Latest</option>
              <option value="LowToHigh">Price low to high</option>
              <option value="HighToLow">Price high to low</option>
            </select>
          </div>
        </div>

        <div class="c-store-gallery">
          <template v-for="(arr, i) in ProductMatrix" :key="arr">
            <template v-if="i == TabIndex">
              <div v-for="product in arr" :key="product" @click="showProduct(product.objectId)" class="c-store-container12 button">
                <img :src="[product.ImagePreview[0] == '' ? 'https://play.teleporthq.io/static/svg/default-img.svg' : product.ImagePreview[0]]" class="c-store-t-s-pimg1" />
                <span class="c-store-text06">
