                    <br />
                    <span></span>
                  </span>
                </span>
                <span class="c-store-text11">Php {{getLowestPrice(product.SKUs)}}</span>
                <span class="c-store-text12">{{product.Sold}} Sold</span>
              </div>
            </template>
          </template>
        </div>
      </div>

      <div v-if="currentTab == 'home'" class="c-store-discover">
