<script>
import Parse from 'parse';
import CHeader from '@/components/c-header'
import AppFooter from '@/components/footer'

export default {
  name: 'CStore',
  components: {
    CHeader,
    AppFooter,
  },

  data() {
    return {
      rawsi75: ' ',
      ShopData: {},
      ShopProducts: [],
      Categories: [],
      currentTab: 'all',
      ShopRatings: [],

      ProductMatrix: [],
      TabIndex: 0,

      categoryTabID: '',
      raw769e: ' ',
    }
  },
  methods: {
    showProduct(productid) {
      this.$router.push({
        name: 'c-product',
        query: {
          ProductID: productid,
        }
      })
    },

    sortProducts(by) {
      by = by.target.value;
      var type;
      if (by == "Latest") type = "createdAt";
      else type = "SKUs";

      this.queryProducts().sort((a, b) => {
        var valueA = a[type];
        var valueB = b[type];

        if (by != "Latest") {
          valueA = this.getLowestPrice(valueA);
          valueB = this.getLowestPrice(valueB);
        }

        if (valueA < valueB) {
          if (by == "HighToLow") return 1;
          return -1;
        }
        if (valueA > valueB) {
          if (by == "HighToLow") return -1;
          return 1;
        }
        return 0;
      });
      this.splitProducts();
    },

    splitProducts() {
      this.ProductMatrix = [];
      var arr = [];
      for (const element of this.queryProducts()) {
        if (arr.length >= 20) {
          this.ProductMatrix.push(arr);
          arr = [];
        }
        arr.push(element);
      }
      if (arr.length > 0) {
        this.ProductMatrix.push(arr);
      }
    },

    getShopAverageRating() {
      var total = 0;
      for (var rating of this.ShopRatings) {
        total += rating.Rating;
      }
      if (total == 0) return total;
      return total / this.ShopRatings.length;
    },

    queryProducts() {
      if (this.categoryTabID != '') {
        return this.ShopProducts.filter((prod) => {
          return prod.CategoryID == this.categoryTabID;
        });
      }
      else {
        return this.ShopProducts;
      }
    },

    switchTab(tab, categoryID = '') {
      this.currentTab = tab;
      this.categoryTabID = categoryID;
      this.splitProducts();
    },

    getLowestPrice(arr) {
      return Math.min(...arr.map(item => item.OriginalPrice))
    },

    async getCategories() {
      this.Categories = JSON.parse(await Parse.Cloud.run("GetCategories"));
    },

    async getShopData(id) {
      this.ShopData = JSON.parse(await Parse.Cloud.run("GetShopData", { "ShopID": id }));
    },

    async getShopProducts(id) {
      var tmp = JSON.parse(await Parse.Cloud.run("GetShopProducts", { "ShopID": id }));
      for await (var prod of tmp) {
        prod["SKUs"] = JSON.parse(await Parse.Cloud.run("GetProductSKUs", { "ProductID": prod.objectId }));
      }
      this.ShopProducts = tmp;
      this.splitProducts();
    },

    async getShopRating(id) {
      this.ShopRatings = JSON.parse(await Parse.Cloud.run("GetShopProductRatings", { "ShopID": id }));
    },
  },

  beforeMount() {
    var id = this.$route.query.ShopID;
    this.getShopData(id);
    this.getShopRating(id);
    this.getShopProducts(id);
    this.getCategories();
  },

  metaInfo: {
    title: 'C-Store - exported project',
    meta: [
      {
        property: 'og:title',
        content: 'C-Store - exported project',
      },
    ],
  },
}
</script>

