<template>
  <div class="c-my-cart-container">
    <c-header rootClassName="c-header-root-class-name2"></c-header>
    <span class="c-my-cart-my-cart">My Cart</span>
    <div class="c-my-cart-cartbody">
      <div class="c-my-cart-columnname">
        <div class="c-my-cart-container01">
          <span class="c-my-cart-text">Product</span>
        </div>
        <div class="c-my-cart-container02">
          <span class="c-my-cart-text01">Unit Price</span>
          <span class="c-my-cart-text02">Quantity</span>
          <span class="c-my-cart-text03">Total Price</span>
          <span class="c-my-cart-text04">Actions</span>
        </div>
      </div>


      <div v-for="(arr, i) in SKUs" :key=i class="c-my-cart-shop1container">
        <div class="c-my-cart-shop1name">
          <div class="c-my-cart-container03">
            <input @click="toggleShopItemCheckBox(ShopsData[i].objectId, i)"
              v-bind:checked="!this.ShopIncluded[i].includes(false)" type="checkbox" class="c-my-cart-checkbox" />
            <button class="c-my-cart-button button">
              <svg viewBox="0 0 1024 1024" class="c-my-cart-icon">
                <path
                  d="M1024 590.444l-512-397.426-512 397.428v-162.038l512-397.426 512 397.428zM896 576v384h-256v-256h-256v256h-256v-384l384-288z">
                </path>
              </svg>
              <span v-if="ShopsData[i] !== undefined" class="c-my-cart-shopname">{{ShopsData[i].ShopName}}</span>
            </button>
          </div>
          <span class="c-my-cart-shopname1">
            <span class="c-my-cart-text05">Edit</span>
            <span class="c-my-cart-text06"></span>
          </span>
        </div>

        <div v-for="(sku, j) in arr" :key="sku" class="c-my-cart-product1">
          <div class="c-my-cart-container04">
            <div class="c-my-cart-container05">
              <input v-model="ShopIncluded[i][j]" @click="toggleItemCheckBox(arr[j].CartID, i, j)" type="checkbox"
                class="c-my-cart-checkbox1" />
              <img :src="[(sku.ImagePreview == '' || sku.ImagePreview == undefined) ? 'https://play.teleporthq.io/static/svg/default-img.svg' : sku.ImagePreview]" class="c-my-cart-cartimg" />
            </div>
            <span class="c-my-cart-productname">{{sku.ProductData.ProductName}}</span>
            <div class="c-my-cart-container06">
              <select v-model="sku.objectId" @change="updateVariation(arr[j].CartID, sku.objectId)"
                class="c-my-cart-variationbtn1">
                <template v-for="sku2 in sku.ProductSKUs" :key="sku2">
                  <option v-bind:value="sku2.objectId">{{sku2.SKU}}</option>
                </template>
              </select>
            </div>
          </div>
          <div class="c-my-cart-container07">
            <span class="c-my-cart-quantitytotalprice">{{sku.OriginalPrice}}</span>
            <div class="c-my-cart-q-u-a-n-t-i-t-y">
              <button @click="changeQuantity(arr[j].CartID, i, j, sku.Quantity, -1)" class="c-my-cart-button01 button">
                <span class="c-my-cart-text07">
                  <span>-</span>
                  <br />
                </span>
              </button>
              <span class="c-my-cart-quantity-indicator">{{sku.Quantity}}</span>
              <button @click="changeQuantity(arr[j].CartID, i, j, sku.Quantity, 1)" class="c-my-cart-button02 button">
                <span class="c-my-cart-text10">+</span>
              </button>
            </div>
            <span class="c-my-cart-productprice">Php {{sku.OriginalPrice * sku.Quantity}}</span>
            <button @click="deleteCart(arr[j].CartID, i, j)" class="c-my-cart-deletecart button">Delete</button>
          </div>
        </div>
      </div>


      <div v-if="this.SKUs.length > 0" class="c-my-cart-cartoptions">
        <div class="c-my-cart-container32">
          <input @click="toggleSelectAll()" type="checkbox" v-bind:checked="checkIfAllSelected()"
            class="c-my-cart-checkbox5" />
          <span class="c-my-cart-text32 button">Select All ({{CheckOutCartIDs.length}})</span>
          <button class="c-my-cart-deletecart3 button">Delete</button>
        </div>
        <div class="c-my-cart-container33">
          <div class="c-my-cart-container34">
            <span class="c-my-cart-text33">Total Order</span>
            <span class="c-my-cart-text34">{{getTotalPrice()}}</span>
          </div>
          <button @click="checkOut()" class="c-my-cart-button14 button">
            <span class="c-my-cart-text35">
              <span>Check Out</span>
              <span></span>
            </span>
          </button>
        </div>
      </div>
      <button class="c-my-cart-removeinactivecart button">
        Remove inactive products
      </button>
    </div>
    <app-footer rootClassName="footer-root-class-name2"></app-footer>
  </div>
</template>

<script>
import Parse from 'parse';
import CHeader from '@/components/c-header'
import AppFooter from '@/components/footer'


export default {
  name: 'CMyCart',
  components: {
    CHeader,
    AppFooter,
  },
  metaInfo: {
    title: 'C-MyCart - exported project',
    meta: [
      {
        property: 'og:title',
        content: 'C-MyCart - exported project',
      },
    ],
  },

  data() {
    return {
      ShopIncluded: [],
      Shops: [],
      ShopsData: [],
      SKUs: [],
      CustomerCartSKUs: [],
      CheckOutCartIDs: [],
    }
  },

  methods: {
    async getCartItems() {
      var params = {
        "CustomerID": Parse.User.current().get("AccountID"),
      }
      var skus = JSON.parse(await Parse.Cloud.run("GetCustomerCart", params));
      this.CustomerCartSKUs = skus;
      for (var sku of skus) {
        this.Shops.push(sku.ShopID);
        this.CheckOutCartIDs.push(sku.objectId);
      }
      var uSet = new Set(this.Shops);
      this.Shops = [...uSet];

      for (var shopID of this.Shops) {
        var arr = [];
        var arr_included = [];
        for (var sku2 of skus) {
          if (sku2.ShopID == shopID) {
            params = { "SKUID": sku2.SKUID };
            var data = JSON.parse(await Parse.Cloud.run("GetSKUData", params));
            data["CartID"] = sku2.objectId;
            data["Quantity"] = sku2.Quantity;
            params = { "ProductID": data.ProductData.objectId };
            data["ProductSKUs"] = JSON.parse(await Parse.Cloud.run("GetProductSKUs", params));
            arr.push(data);
            arr_included.push(true);
          }
        }
        this.SKUs.push(arr);
        this.ShopIncluded.push(arr_included);
      }
    },

    async getShopsData() {
      for (var shopID of this.Shops) {
        var param = { "ShopID": shopID };
        this.ShopsData.push(JSON.parse(await Parse.Cloud.run("GetShopData", param)));
      }
    },

    toggleItemCheckBox(id, i, j) {
      if (this.CheckOutCartIDs.includes(id)) {
        this.removeItem(id, i, j);
      }
      else {
        this.includeItem(id, i, j);
      }
    },

    toggleShopItemCheckBox(id, i) {
      if (this.ShopIncluded[i].includes(false)) {
        for (let j = 0; j < this.SKUs[i].length; ++j) {
          var sku = this.SKUs[i][j];
          if (sku.ShopID == id) {
            this.includeItem(sku.CartID, i, j);
          }
        }
      }
      else {
        for (let j = 0; j < this.SKUs[i].length; ++j) {
          var sku2 = this.SKUs[i][j];
          if (sku2.ShopID == id) {
            this.removeItem(sku2.CartID, i, j);
          }
        }

      }
    },

    getTotalPrice() {
      var total = 0;
      for (var sku of this.SKUs) {
        for (var item of sku) {
          if (this.CheckOutCartIDs.includes(item.CartID)) {
            console.log(this.CheckOutCartIDs, "--", item.CartID);
            total += item.OriginalPrice * item.Quantity;
          }
        }
      }
      return total;
    },

    includeItem(id, i, j) {
      this.CheckOutCartIDs.push(id);
      var uSet = new Set(this.CheckOutCartIDs);
      this.CheckOutCartIDs = [...uSet];
      this.ShopIncluded[i][j] = true;
    },

    removeItem(id, i, j) {
      this.CheckOutCartIDs = this.CheckOutCartIDs.filter(function (e) { return e !== id });
      this.ShopIncluded[i][j] = false;
    },

    checkIfAllSelected() {
      for (const element of this.ShopIncluded) {
        if (element.includes(false)) {
          return false;
        }
      }
      return true;
    },

    toggleSelectAll() {
      var b = this.checkIfAllSelected();
      for (let i = 0; i < this.SKUs.length; ++i) {
        for (let j = 0; j < this.SKUs[i].length; ++j) {
          var sku = this.SKUs[i][j];
          if (b) {
            this.removeItem(sku.CartID, i, j);
          }
          else {
            this.includeItem(sku.CartID, i, j);
          }
        }
      }
    },

    changeQuantity(id, i, j, cur, n) {
      if (cur + n <= 0) return;
      if (cur + n >= 99) return;
      this.SKUs[i][j].Quantity += n;
      this.updateCart(id, cur + n);
    },

    async updateCart(id, quan) {
      var params = {
        "CartID": id,
        "Quantity": quan,
      }
      await Parse.Cloud.run("EditCart", params);
    },

    async updateVariation(cartID, skuID) {
      var params = {
        "CartID": cartID,
        "SKUID": skuID,
      }
      await Parse.Cloud.run("EditCart", params);
    },

    //DELETE ITEM
    async deleteCart(id, i, j) {
      this.CheckOutCartIDs.splice(this.CheckOutCartIDs.indexOf(id), 1);
      this.SKUs[i].splice(j, 1);
      if (this.SKUs[i].length <= 0) this.SKUs.splice(i, 1);
      await Parse.Cloud.run("DeleteCart", { "CartID": id });
    },

    async checkOut() {
      this.$router.push({
        name: 'c-checkout',
        query: {
          CheckOutCartIDs: this.CheckOutCartIDs,
        }
      });
    },

  },

  async beforeMount() {
    await this.getCartItems();
    console.log()
    await this.getShopsData();
    console.log("?");
  },
}
</script>

