          <option value="Latest">Latest</option>
          <option value="LowToHigh">Price low to high</option>
          <option value="HighToLow">Price high to low</option>
        </select>
      </div>
    </div>

    <div class="c-homepage-category-container3">
      <div class="c-homepage-category-gallery">

        <template v-for="(arr, i) in ProductMatrix" :key="arr">
          <template v-if="i == TabIndex">
            <div v-for="product in arr" :key="product">
              <button @click="showProduct(product.objectId)">
                <div class="c-homepage-category-container4 button">
                  <img :src="[product.ImagePreview[0] == '' ? 'https://play.teleporthq.io/static/svg/default-img.svg' : product.ImagePreview[0]]"
                    class="c-homepage-category-t-s-pimg1" />
                  <span class="c-homepage-category-text06">
                    <span class="c-homepage-category-text07">
                      <span>{{ product.ProductName }}</span>
                      <br />
                      <span></span>
                    </span>
                  </span>
                  <span class="c-homepage-category-text11">Php {{getLowestPrice(product.SKUs)}}</span>
                  <span class="c-homepage-category-text12">{{product.Sold}} Sold</span>
                </div>
              </button>
            </div>
          </template>
