<script>
import Parse from 'parse';
import CHeader from '@/components/c-header'
import AppFooter from '@/components/footer'

export default {
  name: 'CHomepageCategory',
  components: {
    CHeader,
    AppFooter,
  },
  metaInfo: {
    title: 'C-HomepageCategory - exported project',
    meta: [
      {
        property: 'og:title',
        content: 'C-HomepageCategory - exported project',
      },
    ],
  },

  data() {
    return {
      rawnu53: ' ',
      rawox4q: ' ',
      rawetbf: ' ',
      CategoryID: '',
      CategoryData: {},
      TabIndex: 0,
      //CurrentTab: '',

      //Product Variables
      Products: [],
      ProductMatrix: [],
      SortedBy: 'Latest',

      //Category
      Categories: [],
    }
  },

  methods: {
    switchCatgeory(id) {
      this.CategoryID = id;
      this.getCategoryData(id);
      this.getProducts(id);
    },

    switchTabIndex(index) {
      this.TabIndex = index
    },

    getLowestPrice(arr) {
      return Math.min(...arr.map(item => item.OriginalPrice))
    },

    sortProducts(by) {
      by = by.target.value;
      var type;
      if (by == "Latest") type = "createdAt";
      else type = "SKUs";

      this.Products.sort((a, b) => {
        var valueA = a[type];
        var valueB = b[type];

        if (by != "Latest") {
          valueA = this.getLowestPrice(valueA);
          valueB = this.getLowestPrice(valueB);
        }

        if (valueA < valueB) {
          if (by == "HighToLow") return 1;
          return -1;
        }
        if (valueA > valueB) {
          if (by == "HighToLow") return -1;
          return 1;
        }
        return 0;
      });
      this.splitProducts();
    },

    splitProducts() {
      this.ProductMatrix = [];
      var arr = [];
      for (const element of this.Products) {
        if (arr.length >= 20) {
          this.ProductMatrix.push(arr);
          arr = [];
        }
        arr.push(element);
      }
      if (arr.length > 0) {
        this.ProductMatrix.push(arr);
      }
    },

    async getCategoryData(CategoryID) {
      var param = { "CategoryID": CategoryID };
      this.CategoryData = JSON.parse(await Parse.Cloud.run("GetCategoryData", param));
    },

    async getProducts(CategoryID) {
      var param = {
        "CategoryID": CategoryID,
        "Limit": 100,
      };
      var tmp = JSON.parse(await Parse.Cloud.run("GetProducts", param));
      for await (var prod of tmp) {
        prod["SKUs"] = JSON.parse(await Parse.Cloud.run("GetProductSKUs", { "ProductID": prod.objectId }));
      }

      //TESTING
