        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
        tmp2.push(element);
      }
      tmp = tmp2;
      */
      //End of testing

      this.Products = tmp;
      this.splitProducts();
    },

    async getCategories() {
      this.Categories = JSON.parse(await Parse.Cloud.run("GetCategories"));
    },

    showProduct(productid) {
      this.$router.push({
        name: 'c-product',
        query: {
          ProductID: productid,
        }
      })
    },
  },

  async beforeMount() {
    var id = this.$route.query.CategoryID;
    await this.getCategories();
    console.log(id);
    if (id == undefined) {
      id = this.Categories[0].objectId;
    }
    this.CategoryID = id
    this.getCategoryData(id);
    this.getProducts(id);
  },
}
