<template>
  <div class="c-create-ticket-container">
    <c-header rootClassName="c-header-root-class-name22"></c-header>
    <span class="c-create-ticket-text">Submit a Ticket</span>
    <div class="c-create-ticket-container1">
      <div class="c-create-ticket-container2">
        <div class="c-create-ticket-container3">
          <span class="c-create-ticket-text1">Category of Concern</span>
          <select v-model="CategoryID" class="c-create-ticket-button button">
            <option value="" hidden>Select Category of Concern</option>
            <template v-for="category in CatgeoriesOfConcern" :key="category">
              <option :value="category.objectId" class="c-customer-create-ticket-text3">
                {{category.Category}}
              </option>
            </template>
          </select>
        </div>
        <div class="c-create-ticket-container4">
          <span class="c-create-ticket-text3">Main Concern</span>
          <textarea v-model="Concern" class="c-create-ticket-textarea textarea"></textarea>
          <button @click="createTicket()" class="c-create-ticket-button1 button">Submit Ticket</button>
        </div>
      </div>
    </div>
    <app-footer rootClassName="footer-root-class-name24"></app-footer>
  </div>
</template>

<script>
import Parse from 'parse';
import CHeader from '@/components/c-header'
import AppFooter from '@/components/footer'

export default {
  name: 'CCreateTicket',
  components: {
    CHeader,
    AppFooter,
  },
  metaInfo: {
    title: 'C-Create-Ticket - exported project',
    meta: [
      {
        property: 'og:title',
        content: 'C-Create-Ticket - exported project',
      },
    ],
  },

  data() {
    return {
      Concern: '',
      CategoryID: '',
      CatgeoriesOfConcern: [],
    }
  },

  methods: {
    async getCatgeoriesOfConcern() {
      this.CatgeoriesOfConcern = JSON.parse(await Parse.Cloud.run("GetCategoriesOfConcern"));
    },

    async createTicket() {
      var param = {
        "CustomerID": Parse.User.current().get("AccountID"),
        "CategoryID": this.CategoryID,
        "Concern": this.Concern,
      }
      await Parse.Cloud.run("AddTicket", param).then(() => {
        alert("Ticket Submited");
      });
    }
  },

  beforeMount() {
    this.getCatgeoriesOfConcern();
  },
}
</script>

